	module.exports = " <form action=\"//network.us14.list-manage.com/subscribe/post?u=1b8aeaa81ca615914eb2eb7fc&amp;id=64c73f9f5f\" method=post id=mc-embedded-subscribe-form name=mc-embedded-subscribe-form class=validate target=_blank novalidate=\"\" _v-663026ac=\"\"> <div class=mc-field-group _v-663026ac=\"\"> <input type=email value=\"\" name=EMAIL class=\"required email\" id=mce-EMAIL placeholder=\"Email Address\" _v-663026ac=\"\"> </div> <div class=mc-field-group _v-663026ac=\"\"> <input type=submit value=\"Alert Me\" name=subscribe id=mc-embedded-subscribe _v-663026ac=\"\"> </div> <div id=mce-responses class=clear _v-663026ac=\"\"> <div class=response id=mce-error-response style=display:none _v-663026ac=\"\"></div> <div class=response id=mce-success-response style=display:none _v-663026ac=\"\"></div> </div> <div style=\"position: absolute; left: -5000px\" aria-hidden=true _v-663026ac=\"\"><input type=text name=b_1b8aeaa81ca615914eb2eb7fc_64c73f9f5f tabindex=-1 value=\"\" _v-663026ac=\"\"></div> </form> ";

