k of contentious hard-forks. Anyone could plug in a better smart contract system.</p> </div> <walkthrough-nav step=4 _v-75a39e68=\"\"></walkthrough-nav> </div></section> <section id=walkthrough-5 class=section-slide _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <div class=image _v-75a39e68=\"\"><img src=" + require("../../assets/images/ex_dist_exchange.png") + " _v-75a39e68=\"\"></div> <div class=section-content _v-75a39e68=\"\"> <p _v-75a39e68=\"\">We could import other blockchains and have a distributed exchange on its own zone.</p> </div> <walkthrough-nav step=5 _v-75a39e68=\"\"></walkthrough-nav> </div></section> <section id=walkthrough-6 class=section-slide _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <div class=image _v-75a39e68=\"\"><img src=" + require("../../assets/images/ex_network.png") + " _v-75a39e68=\"\"></div> <div class=section-content _v-75a39e68=\"\"> <p _v-75a39e68=\"\">The Cosmos hub isn’t the center of the universe. Any zone can be a hub.</p> </div> <walkthrough-nav step=6 _v-75a39e68=\"\"></walkthrough-nav> </div></section> ";


