module.exports = " <div class=header-padding _v-db453a86=\"\"></div> <div class=article-wrapper _v-db453a86=\"\"> <h1 id=cosmos _v-db453a86=\"\">Cosmos</h1> <p _v-db453a86=\"\"><strong _v-db453a86=\"\">A Network of Distributed Ledgers</strong></p> <p _v-db453a86=\"\">Jae Kwon jae@tendermint.com<br _v-db453a86=\"\"> Ethan Buchman ethan@tendermint.com</p> <p _v-db453a86=\"\">For discussions, <a href=http://forum.tendermint.com:3000/ _v-db453a86=\"\">join our Slack</a>!</p> <p _v-db453a86=\"\"><em _v-db453a86=\"\">NOTE: If you can read this on GitHub, then we're still actively developing this document. Please check regularly for updates!.</em></p> <h2 id=table-of-contents _v-db453a86=\"\">Table of Contents</h2> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#introduction _v-db453a86=\"\">Introduction</a></li> <li _v-db453a86=\"\"><a href=#tendermint _v-db453a86=\"\">Tendermint</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#validators _v-db453a86=\"\">Validators</a></li> <li _v-db453a86=\"\"><a href=#consensus _v-db453a86=\"\">Consensus</a></li> <li _v-db453a86=\"\"><a href=#light-clients _v-db453a86=\"\">Light Clients</a></li> <li _v-db453a86=\"\"><a href=#preventing-attacks _v-db453a86=\"\">Preventing Attacks</a></li> <li _v-db453a86=\"\"><a href=#tmsp _v-db453a86=\"\">TMSP</a></li> </ul> </li> <li _v-db453a86=\"\"><a href=#cosmos-overview _v-db453a86=\"\">Cosmos Overview</a></li> <li _v-db453a86=\"\"><a href=#the-hub-and-zones _v-db453a86=\"\">The Hub and Zones</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#the-hub _v-db453a86=\"\">The Hub</a></li> <li _v-db453a86=\"\"><a href=#the-zones _v-db453a86=\"\">The Zones</a></li> </ul> </li> <li _v-db453a86=\"\"><a href=#inter-blockchain-communication-ibc _v-db453a86=\"\">Inter-blockchain Communication (IBC)</a></li> <li _v-db453a86=\"\"><a href=#use-cases _v-db453a86=\"\">Use Cases</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#pegging-to-other-cryptocurrencies _v-db453a86=\"\">Pegging to Other Cryptocurrencies</a></li> <li _v-db453a86=\"\"><a href=#ethereum-scaling _v-db453a86=\"\">Ethereum Scaling</a></li> <li _v-db453a86=\"\"><a href=#multi-application-integration _v-db453a86=\"\">Multi-Application Integration</a></li> <li _v-db453a86=\"\"><a href=#network-partition-mitigation _v-db453a86=\"\">Network Partition Mitigation</a></li> <li _v-db453a86=\"\"><a href=#federated-name-resolution-system _v-db453a86=\"\">Federated Name Resolution System</a></li> </ul> </li> <li _v-db453a86=\"\"><a href=#issuance-and-incentives _v-db453a86=\"\">Issuance and Incentives</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#the-atom-token _v-db453a86=\"\">The Atom Token</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#crowdfund _v-db453a86=\"\">Crowdfund</a></li> <li _v-db453a86=\"\"><a href=#vesting _v-db453a86=\"\">Vesting</a></li> </ul> </li> <li _v-db453a86=\"\"><a href=#limitations-on-the-number-of-validators _v-db453a86=\"\">Limitations on the Number of Validators</a></li> <li _v-db453a86=\"\"><a href=#becoming-a-validator-after-genesis-day _v-db453a86=\"\">Becoming a Validator After Genesis Day</a></li> <li _v-db453a86=\"\"><a href=#penalties-for-validators _v-db453a86=\"\">Penalties for Validators</a></li> <li _v-db453a86=\"\"><a href=#transaction-fees _v-db453a86=\"\">Transaction Fees</a></li> <li _v-db453a86=\"\"><a href=#incentivizing-hackers _v-db453a86=\"\">Incentivizing Hackers</a></li> </ul> </li> <li _v-db453a86=\"\"><a href=#governance _v-db453a86=\"\">Governance</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#parameter-change-proposal _v-db453a86=\"\">Parameter Change Proposal</a></li> <li _v-db453a86=\"\"><a href=#text-proposal _v-db453a86=\"\">Text Proposal</a></li> </ul> </li> <li _v-db453a86=\"\"><a href=#roadmap _v-db453a86=\"\">Roadmap</a></li> <li _v-db453a86=\"\"><a href=#related-work _v-db453a86=\"\">Related Work</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#consensus-systems _v-db453a86=\"\">Consensus Systems</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#classic-byzantine-fault-tolerance _v-db453a86=\"\">Classic Byzantine Fault Tolerance</a></li> <li _v-db453a86=\"\"><a href=#bitshares-delegated-stake _v-db453a86=\"\">BitShares delegated stake</a></li> <li _v-db453a86=\"\"><a href=#stellar _v-db453a86=\"\">Stellar</a></li> <li _v-db453a86=\"\"><a href=#bitcoinng _v-db453a86=\"\">BitcoinNG</a></li> <li _v-db453a86=\"\"><a href=#casper _v-db453a86=\"\">Casper</a></li> </ul> </li> <li _v-db453a86=\"\"><a href=#horizontal-scaling _v-db453a86=\"\">Horizontal Scaling</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#interledger-protocol _v-db453a86=\"\">Interledger Protocol</a></li> <li _v-db453a86=\"\"><a href=#sidechains _v-db453a86=\"\">Sidechains</a></li> <li _v-db453a86=\"\"><a href=#ethereum-scalability-efforts _v-db453a86=\"\">Ethereum Scalability Efforts</a></li> </ul> </li> <li _v-db453a86=\"\"><a href=#general-scaling _v-db453a86=\"\">General Scaling</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#lightning-network _v-db453a86=\"\">Lightning Network</a></li> <li _v-db453a86=\"\"><a href=#segregated-witness _v-db453a86=\"\">Segregated Witness</a></li> </ul> </li> </ul> </li> <li _v-db453a86=\"\"><a href=#appendix _v-db453a86=\"\">Appendix</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#fork-accountability _v-db453a86=\"\">Fork Accountability</a></li> <li _v-db453a86=\"\"><a href=#tendermint-consensus _v-db453a86=\"\">Tendermint Consensus</a></li> <li _v-db453a86=\"\"><a href=#tendermint-light-clients _v-db453a86=\"\">Tendermint Light Clients</a></li> <li _v-db453a86=\"\"><a href=#preventing-long-range-attacks _v-db453a86=\"\">Preventing Long Range Attacks</a></li> <li _v-db453a86=\"\"><a href=#overcoming-forks-and-censorship-attacks _v-db453a86=\"\">Overcoming Forks and Censorship Attacks</a></li> <li _v-db453a86=\"\"><a href=#tmsp-specification _v-db453a86=\"\">TMSP Specification</a></li> <li _v-db453a86=\"\"><a href=#ibc-packet-delivery-acknowledgement _v-db453a86=\"\">IBC Packet Delivery Acknowledgement</a></li> <li _v-db453a86=\"\"><a href=#merkle-tree--proof-specification _v-db453a86=\"\">Merkle tree &amp; proof specification</a></li> <li _v-db453a86=\"\"><a href=#transaction-types _v-db453a86=\"\">Transaction Types</a><ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=#ibcblockcommittx _v-db453a86=\"\">IBCBlockCommitTx</a></li> <li _v-db453a86=\"\"><a href=#ibcpackettx _v-db453a86=\"\">IBCPacketTx</a></li> </ul> </li> </ul> </li> <li _v-db453a86=\"\"><a href=#acknowledgements _v-db453a86=\"\">Acknowledgements</a></li> <li _v-db453a86=\"\"><a href=#citations _v-db453a86=\"\">Citations</a></li> </ul> <h2 id=introduction _v-db453a86=\"\">Introduction</h2> <p _v-db453a86=\"\">The combined success of the open-source ecosystem, of decentralized file-sharing, and of public cryptocurrencies, has inspired an understanding that decentralized internet protocols can be used to radically improve socio-economic infrastructure. We have seen specialized blockchain applications like Bitcoin <a href=https://bitcoin.org/bitcoin.pdf _v-db453a86=\"\">[1]</a> (a cryptocurrency), Zerocash <a href=http://zerocash-project.org/paper _v-db453a86=\"\">[2]</a> (a cryptocurrency for privacy), and generalized smart contract platforms such as Ethereum <a href=https://github.com/ethereum/wiki/wiki/White-Paper _v-db453a86=\"\">[3]</a>, with countless distributed applications for the EVM such as Augur (a prediction market) and TheDAO <a href=https://download.slock.it/public/DAO/WhitePaper.pdf _v-db453a86=\"\">[4]</a> (an investment club).</p> <p _v-db453a86=\"\">To date, however, these blockchains have suffered from a number of drawbacks, including their gross energy inefficiency, poor or limited performance, and immature governance mechanisms. A number of proposals have been made to scale Bitcoin's transaction throughput such as Segregated-Witness <a href=https://github.com/bitcoin/bips/blob/master/bip-0141.mediawiki _v-db453a86=\"\">[5]</a> and BitcoinNG <a href=https://arxiv.org/pdf/1510.02037v2.pdf _v-db453a86=\"\">[6]</a>, but these are vertical scaling solutions that remain limited by the capacity of a single physical machine, lest we sacrifice the property of complete auditability. The Lightning Network <a href=https://lightning.network/lightning-network-paper-DRAFT-0.5.pdf _v-db453a86=\"\">[7]</a> can help scale Bitcoin transaction volume by leaving some transactions off the ledger completely and is well suited for micropayments and privacy preserving payment rails, but may not be suitable for more generalized scaling needs.</p> <p _v-db453a86=\"\">An ideal solution would be one that allows multiple parallel blockchains to interoperate while retaining their security properties, but this has proven difficult, if not impossible, with proof-of-work. Merged-mining, for instance, allows the work done to secure a parent chain to be re-used on a child chain, but transactions still must be validated, in order, by each node, and a merge-mined blockchain is vulnerable to attack if a majority of the hashing power on the parent is not actively merge-mining the child. An academic review of <a href=http://vukolic.com/iNetSec_2015.pdf _v-db453a86=\"\">alternative blockchain network architectures</a> is provided for additional context, and we provide more summaries of some proposals and their drawbacks in <a href=#related-work _v-db453a86=\"\">Related Work</a>.</p> <p _v-db453a86=\"\">Here we present Cosmos, a novel blockchain network architecture that addresses all of these problems. Cosmos is a network of many independent blockchains, called zones. The zones are powered by Tendermint Core <a href=https://github.com/tendermint/tendermint/wiki _v-db453a86=\"\">[8]</a>, which provides a high-performance, consistent, secure <a href=http://tendermint.com/blog/tendermint-vs-pbft/ _v-db453a86=\"\">PBFT-like</a> consensus engine, where strict <a href=#fork-accountability _v-db453a86=\"\">fork-accountability</a> guarantees hold over the behaviour of malicious actors. Tendermint Core's BFT consensus algorithm is well suited for scaling public proof-of-stake blockchains.</p> <p _v-db453a86=\"\">The first zone on Cosmos is called the Cosmos Hub. The Cosmos Hub is a multi-asset proof-of-stake cryptocurrency with a simple governance mechanism enabling the network to adapt and upgrade. In addition, the Cosmos Hub can be extended by connecting other zones.</p> <p _v-db453a86=\"\">The hub and zones of the Cosmos network communicate with each other via an inter-blockchain communication (IBC) protocol, a kind of virtual UDP or TCP for blockchains. Tokens can be transferred from one zone to another, securely and quickly, without the need for exchange liquidity between zones. Instead, all inter-zone token transfers go through the Cosmos Hub, which keeps track of the total amount of tokens held by each zone. The hub isolates each zone from the failure of other zones. Since anyone can connect a new zone to the Cosmos Hub, zones allow for future-compatibility with new blockchain innovations.</p> <h2 id=tendermint _v-db453a86=\"\">Tendermint</h2> <p _v-db453a86=\"\">In this section we describe the Tendermint consensus protocol and the interface used to build applications with it. For more details, see the <a href=#appendix _v-db453a86=\"\">appendix</a>.</p> <h3 id=validators _v-db453a86=\"\">Validators</h3> <p _v-db453a86=\"\">In classical Byzantine fault-tolerant (BFT) algorithms, each node has the same weight. In Tendermint, nodes have a non-negative amount of <em _v-db453a86=\"\">voting power</em>, and nodes that have positive voting power are called <em _v-db453a86=\"\">validators</em>. Validators participate in the consensus protocol by broadcasting cryptographic signatures, or <em _v-db453a86=\"\">votes</em>, to agree upon the next block.</p> <p _v-db453a86=\"\">Validators' voting powers are determined at genesis, or is changed deterministically by the blockchain, depending on the application. For example, in a proof-of-stake application such as the Cosmos Hub, the voting power may be determined by the amount of staking tokens bonded as collateral.</p> <p _v-db453a86=\"\"><em _v-db453a86=\"\">NOTE: Fractions like ⅔ and ⅓ refer to fractions of the total voting power, never the total number of validators, unless all the validators have equal weight.</em> <em _v-db453a86=\"\">NOTE: +⅔ means \"more than ⅔\", while ⅓+ means \"⅓ or more\".</em></p> <h3 id=consensus _v-db453a86=\"\">Consensus</h3> <p _v-db453a86=\"\">Tendermint is a partially synchronous BFT consensus protocol derived from the DLS consensus algorithm <a href=http://groups.csail.mit.edu/tds/papers/Lynch/jacm88.pdf _v-db453a86=\"\">[20]</a>. Tendermint is notable for its simplicity, performance, and <a href=#fork-accountability _v-db453a86=\"\">fork-accountability</a>. The protocol requires a fixed, known set of validators, where each validator is identified by its public key. Validators attempt to come to consensus on one block at a time, where a block is a list of transactions. Consensus on a block proceeds in rounds. Each round has a round-leader, or proposer, who proposes a block. The validators then vote, in stages, on whether or not to accept the proposed block or move on to the next round. The proposer for a round is chosen deterministically from the ordered list of validators, in proportion to their voting power.</p> <p _v-db453a86=\"\">The full details of the protocol are described <a href=https://github.com/tendermint/tendermint/wiki/Byzantine-Consensus-Algorithm _v-db453a86=\"\">here</a>.</p> <p _v-db453a86=\"\">Tendermint’s security derives from its use of optimal Byzantine fault-tolerance via super-majority (+⅔) voting and a locking mechanism. Together, they ensure that:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\">⅓+ voting power must be Byzantine to cause a violation of safety, where more than two values are committed. </li> <li _v-db453a86=\"\">if ever any set of validators succeeds in violating safety, or even attempts to do so, they can be identified by the protocol. This includes both voting for conflicting blocks and broadcasting unjustified votes.</li> </ul> <p _v-db453a86=\"\">Despite its strong guarantees, Tendermint provides exceptional performance. In benchmarks of 64 nodes distributed across 7 datacenters on 5 continents, on commodity cloud instances, Tendermint consensus can process thousands of transactions per second, with commit latencies on the order of one or two seconds. Notably, performance of well over a thousand transactions per second is maintained even in harsh adversarial conditions, with validators crashing or broadcasting maliciously crafted votes. See figure below for details.</p> <p _v-db453a86=\"\">![Figure of Tendermint throughput performance] (<a href=https://raw.githubusercontent.com/gnuclear/atom-whitepaper/master/images/tendermint_throughput_blocksize.png _v-db453a86=\"\">https://raw.githubusercontent.com/gnuclear/atom-whitepaper/master/images/tendermint_throughput_blocksize.png</a>)</p> <h3 id=light-clients _v-db453a86=\"\">Light Clients</h3> <p _v-db453a86=\"\">A major benefit of Tendermint's consensus algorithm is simplified light client security, making it an ideal candidate for mobile and internet-of-things use cases. While a Bitcoin light client must sync chains of block headers and find the one with the most proof of work, Tendermint light clients need only to keep up with changes to the validator set, and then simply verify the +⅔ PreCommits in the latest block to determine the latest state.</p> <p _v-db453a86=\"\">Succinct light client proofs also enable <a href=#inter-blockchain-communication-ibc _v-db453a86=\"\">inter-blockchain communication</a>.</p> <h3 id=preventing-attacks _v-db453a86=\"\">Preventing Attacks</h3> <p _v-db453a86=\"\">Tendermint has various protective measures for preventing certain notable attacks, like <a href=#preventing-long-range-attacks _v-db453a86=\"\">long-range-nothing-at-stake double spends</a> and <a href=#overcoming-forks-and-censorship-attacks _v-db453a86=\"\">censorship</a>. These are discussed more fully in the <a href=#appendix _v-db453a86=\"\">appendix</a>.</p> <h3 id=tmsp _v-db453a86=\"\">TMSP</h3> <p _v-db453a86=\"\">The Tendermint consensus algorithm is implemented in a program called Tendermint Core. Tendermint Core is an application-agnostic \"consensus engine\" that can turn any deterministic blackbox application into a distributedly replicated blockchain. As the Apache Web Server or Nginx connects to the Wordpress application via CGI or FastCGI, Tendermint Core connects to blockchain applications via the Tendermint Socket Protocol (TMSP) <a href=https://github.com/tendermint/tmsp _v-db453a86=\"\">[17]</a>. Thus, TMSP allows for blockchain applications to be programmed in any language, not just the programming language that the consensus engine is written in. Additionally, TMSP makes it possible to easily swap out the consensus layer of any existing blockchain stack.</p> <p _v-db453a86=\"\">We draw an analogy with a well-known cryptocurrency, Bitcoin. Bitcoin is a cryptocurrency blockchain where each node maintains a fully audited Unspent Transaction Output (UTXO) database. If one wanted to create a Bitcoin-like system on top of TMSP, Tendermint Core would be responsible for</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\">Sharing blocks and transactions between nodes</li> <li _v-db453a86=\"\">Establishing a canonical/immutable order of transactions (the blockchain)</li> </ul> <p _v-db453a86=\"\">Meanwhile, the TMSP application would be responsible for</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\">Maintaining the UTXO database</li> <li _v-db453a86=\"\">Validating cryptographic signatures of transactions</li> <li _v-db453a86=\"\">Preventing transactions from spending non-existent transactions</li> <li _v-db453a86=\"\">Allowing clients to query the UTXO database.</li> </ul> <p _v-db453a86=\"\">Tendermint is able to decompose the blockchain design by offering a very simple API between the application process and consensus process.</p> <h2 id=cosmos-overview _v-db453a86=\"\">Cosmos Overview</h2> <p _v-db453a86=\"\">Cosmos is a network of independent parallel blockchains that are each powered by classical BFT consensus algorithms like Tendermint <a href=http://github.com/tendermint/tendermint _v-db453a86=\"\">1</a>.</p> <p _v-db453a86=\"\">The first blockchain in this network will be the Cosmos Hub. The Cosomos Hub connects to many other blockchains (or <em _v-db453a86=\"\">zones</em>) via a novel inter-blockchain communication protocol. The Cosmos Hub tracks numerous token types, and keeps record of the total number of tokens in each connected zone. Tokens can be transferred from one zone to another, securely and quickly, without the need for a liquid exchange between zones, because all inter-zone coin transfers go through the Cosmos Hub.</p> <p _v-db453a86=\"\">This architecture solves so many problems that the blockchain space faces today, including application interoperability, scalability, and seamless upgradability. For example, zones derived from Bitcoind, Go-Ethereum, CryptoNote, ZCash, or any blockchain system can be plugged into the Cosmos Hub. These zones allow Cosmos to scale infinitely to meet global transaction demand. And, zones are a great fit for a distributed exchange, which will be supported as well.</p> <p _v-db453a86=\"\">Cosmos is not just a single distributed ledger, and the Cosmos Hub isn't a walled garden or the center of its universe. We are designing a protocol for an open network of distributed legers that can serve as a new foundation for our future financial systems, based on principles of cryptography, sound economics, consensus theory, transparency, and accountability.</p> <h3 id=tendermint-bft-dpos _v-db453a86=\"\">Tendermint-BFT DPoS</h3> <p _v-db453a86=\"\">The Cosmos Hub is the first public blockchain in the Cosmos Network powered by Tendermint's BFT consensus algorithm. The Tendermint open-source project was born in 2014 to address the speed, scalability, and environmental issues of Bitcoin's proof-of-work consensus algorithm. By utilizing and improving upon proven BFT algorithms developed at MIT in 1988, the Tendermint team was first to conceptually demonstrate a proof-of-stake cryptocurrency that addresses the nothing-at-stake problem suffered by first-gen proof-of-stake cryptocurrencies such as NXT and BitShares.</p> <p _v-db453a86=\"\">Today, practically all Bitcoin mobile wallets use trusted servers to provide them with transaction verification. This is because proof-of-work requires waiting for many confirmations before a transaction can be considered irreversibly committed. Double-spend attacks have already been demonstrated on services like CoinBase.</p> <p _v-db453a86=\"\">Unlike other blockchain consensus systems, Tendermint offers instant and provably-secure mobile-client payment verification. Since the Tendermint is designed not to fork at all, mobile wallets can receive instant transaction confirmation, finally making trustless and practical payments a reality on your smartphone. This has significant ramifications for IoT applications as well.</p> <p _v-db453a86=\"\">Validators in Cosmos (who have a similar role to Bitcoin miners but instead use cryptographic signatures to vote) are meant to be secure, dedicated machines that are responsible for committing blocks. Non-validators can delegate their staking tokens (called \"atoms\") to any validator to earn a portion of block fees and atom rewards, but incur the risk of getting punished (slashed) if the delegate validator gets hacked or violates the protocol. The proven safety guarantees of Tendermint BFT consensus, and the collateral deposit of stakeholders (of validators and delegators) provide provable, quantifiable security for nodes and even light clients.</p> <h3 id=governance _v-db453a86=\"\">Governance</h3> <p _v-db453a86=\"\">Distributed public ledgers should have a constitution and a governance system. Bitcoin relies on the Bitcoin Foundation (to some degree) and mining to coordinate upgrades, but this is a slow process. Ethereum split into ETH and ETC after the hard-fork to address TheDAO hack, largely because there was no prior social contract nor mechanism for making such decisions.</p> <p _v-db453a86=\"\">Validators and delegators on the Cosmos Hub can vote on proposals that can change preset parameters of the system automatically (such as the block gas limit), coordinate upgrades, as well as vote on amendments to a human-readible constitution that govern the policies of the Cosmos Hub. The constitution allows for cohesion among the stakeholders on issues regarding issues of theft and bugs (such as TheDAO incident), allowing for quicker and cleaner resolution.</p> <p _v-db453a86=\"\">Each zone can also have their own constitution and governance mechanism as well. For example, the Cosmos Hub could have a constitution that enforces immutability at the Hub (no roll-backs, save for bugs of the Cosmos Hub node implementation), while each zone can set their own policies regarding roll-backs in the case of theft and bugs.</p> <p _v-db453a86=\"\">By enabling interoperability among differing policy zones, the Cosmos network gives the users ultimate freedom and potential for permissionless experimentation.</p> <h2 id=the-hub-and-zones _v-db453a86=\"\">The Hub and Zones</h2> <p _v-db453a86=\"\">Here we describe a novel model of decentralization and scalability. Cosmos is a network of many blockchains powered by Tendermint. While existing proposals aim to create a \"single blockchain\" with total global transaction ordering, Cosmos permits many blockchains to run concurrently with one another and yet retain interoperability.</p> <p _v-db453a86=\"\">At the basis, the Cosmos Hub manages many independent blockchains called \"zones\" (sometimes referred to as \"shards\", in reference to the database scaling technique known as \"sharding\"). A constant stream of recent block commits from zones posted on the hub allows the hub to keep up with the state of each zone. Likewise, each zone keeps up with the state of the hub (but zones do not keep up with each other except indirectly through the hub). Packets of information are then communicated from one zone to another by posting Merkle-proofs as evidence that the information was sent and received. This mechanism is called inter-blockchain communication, or IBC for short.</p> <p _v-db453a86=\"\"><img src=https://raw.githubusercontent.com/gnuclear/atom-whitepaper/master/images/hub_and_zones.png alt=\"Figure of hub and zones\nacknowledgement\" _v-db453a86=\"\"></p> <p _v-db453a86=\"\">Any of the zones can themselves be hubs to form an acyclic graph, but for the sake of clarity we will only describe the simple configuration where there is only one hub, and many non-hub zones.</p> <h3 id=the-hub _v-db453a86=\"\">The Hub</h3> <p _v-db453a86=\"\">The Cosmos Hub is a blockchain that hosts a multi-asset distributed ledger, where tokens can be held by individual users or by zones themselves. These tokens can be moved from one zone to another in a special IBC packet called a \"coin packet\". The hub is responsible for preserving the global invariance of the total amount of each token across the zones. IBC coin packet transactions must be committed by the sender, hub, and receiver blockchains.</p> <p _v-db453a86=\"\">Since the Cosmos Hub acts as a central ledger of tokens for the whole system, the security of the hub is of paramount importance. While each zone may be a Tendermint blockchain that is secured by as few as 4 (or even less if BFT consensus is not needed), the hub must be secured by a globally decentralized set of validators that can withstand the most severe attack scenarios, such as a continental network partition or a nation-state sponsored attack.</p> <h3 id=the-zones _v-db453a86=\"\">The Zones</h3> <p _v-db453a86=\"\">A Cosmos zone is an independent blockchain that exchanges IBC messages with the Hub. From the Hub's perspective, a zone is a multi-asset dynamic-membership multi-signature account that can send and receive tokens using IBC packets. Like a cryptocurrency account, a zone cannot transfer more tokens than it has, but can receive tokens from others who have them. A zone may be designated as an \"source\" of one or more token types, granting it the power to inflate that token supply.</p> <p _v-db453a86=\"\">Atoms of the Cosmos Hub may be staked by validators of a zone connected to the hub. While double-spend attacks on these zones would result in the slashing of atoms with Tendermint's fork-accountability, a zone where +⅔ of the voting power are Byzantine can commit invalid state. The Cosmos Hub does not verify or execute transactions committed on other zones, so it is the responsibility of users to send tokens to zones that they trust. In the future, the Cosmos Hub's governance system may pass Hub improvement proposals that account for zone failures. For example, outbound token transfers from some (or all) zones may be throttled to allow for the emergency circuit-breaking of zones (a temporary halt of token transfers) when an attack is detected.</p> <h2 id=inter-blockchain-communication-ibc- _v-db453a86=\"\">Inter-blockchain Communication (IBC)</h2> <p _v-db453a86=\"\">Now we look at how the hub and zones communicate with each other. Say that there are three blockchains, \"Zone1\", \"Zone2\", and \"Hub\", and we wish for \"Zone1\" to produce a packet destined for \"Zone2\" going through \"Hub\". For a packet to move from one blockchain to another, a proof is posted on the receiving chain that the sending chain published a packet for the alleged destination. For the receiving chain to check this proof, it must be able keep up with the sender's block headers. This mechanism is similar to that used by sidechains, requiring two interacting chains to \"be aware\" of one another via a bidirectional stream of proof-of-existence datagrams (transactions).</p> <p _v-db453a86=\"\">The IBC protocol can naturally be defined using two types of transactions: an <code _v-db453a86=\"\">IBCBlockCommitTx</code> transaction, which allows a blockchain to prove to any observer of its most recent block-hash, and an <code _v-db453a86=\"\">IBCPacketTx</code> transaction, which allows a blockchain to prove to any observer that the given packet was indeed published by the sender's application via a Merkle-proof to the recent block-hash.</p> <p _v-db453a86=\"\">By splitting the IBC mechanics into two separate transactions <code _v-db453a86=\"\">IBCBlockCommitTx</code> and <code _v-db453a86=\"\">IBCPacketTx</code>, we allow the native fee market-mechanism of the receiving chain to determine which packets get committed (i.e. acknowledged), while allowing for complete freedom on the sending chain as to how many outbound packets are allowed.</p> <p _v-db453a86=\"\"><img src=https://raw.githubusercontent.com/gnuclear/atom-whitepaper/master/msc/ibc_without_ack.png alt=\"Figure of Zone1, Zone2, and Hub IBC without\nacknowledgement\" _v-db453a86=\"\"></p> <p _v-db453a86=\"\"> In the example above, in order to update the block-hash of \"Zone1\" on \"Hub\" (or of \"Hub\" on \"Zone2\"), an <code _v-db453a86=\"\">IBCBlockCommitTx</code> transaction must be posted on \"Hub\" with the block-hash of \"Zone1\" (or on \"Zone2\" with the block-hash of \"Hub\").</p> <p _v-db453a86=\"\"><em _v-db453a86=\"\">See <a href=#ibcblockcommittx _v-db453a86=\"\">IBCBlockCommitTx</a> and <a href=#ibcpacketcommit _v-db453a86=\"\">IBCPacketTx</a> for for more information on the two IBC transaction types.</em></p> <h2 id=use-cases _v-db453a86=\"\">Use Cases</h2> <h3 id=pegging-to-other-cryptocurrencies _v-db453a86=\"\">Pegging to Other Cryptocurrencies</h3> <p _v-db453a86=\"\">A priveleged zone can act as the source of a pegged token of another cryptocurrency. A peg is in essence similar to the relationship between an Cosmos hub and zone; both must keep up with the latest blocks of the other in order to verify proofs that tokens have moved from one to the other. A peg-zone on the Cosmos network keeps up with both the hub as well as the other cryptocurrency. The indirection through the peg-zone allows the logic of the hub to remain simple and agnostic to other blockchain consensus strategies, like Bitcoin's proof-of-work mining.</p> <p _v-db453a86=\"\">For instance, a Cosmos zone with some validator set, possibly the same as that of the hub, could act as an ether-peg, where the TMSP-application on the zone (the \"peg-zone\") has mechanisms to exchange IBC messages with a peg-contract on the external Ethereum blockchain (the \"origin\"). This contract would allow ether holders to send ether to the peg-zone by sending it to the peg-contract on Ethereum. Once ether is received by the peg-contract, the ether cannot be withdrawn unless an appropriate IBC packet is received by the peg-contract from the peg-zone. When a peg-zone receives an IBC packet proving that ether was received in the peg-contract for a particular Ethereum account, a corresponding account is created on the peg-zone with that balance. Ether on the peg-zone (\"pegged-ether\") can then be transferred to and from the hub, and later be destroyed with a transaction that sends it to a particular withdrawal address on Ethereum; an IBC packet proving that the transaction occured on the peg-zone can be posted to the Ethereum peg-contract to allow the ether to be withdrawn.</p> <p _v-db453a86=\"\">Of course, the risk of such a pegging contract is a rogue validator set. ⅓+ Byzantine voting power could cause a fork, withdrawing ether from the peg-contract on Ethereum while keeping the pegged-ether on the peg-zone. Worse, +⅔ Byzantine voting power can steal ether outright from those who sent it to the peg-contract by deviating from the original pegging logic of the peg-zone.</p> <p _v-db453a86=\"\">It is possible to address these issues by designing the peg to be \"totally accountable\". For example, all IBC packets both from the hub as well as from the origin might require acknowledgement by the peg-zone in such a way that all state transitions of the peg-zone can be efficiently challenged and verified by either the hub or the origin's peg-contract. The hub and the origin should allow the peg-zone validators to post collateral, and token transfers out of the peg-contract should be delayed (and collateral unbonding period sufficiently long) to allow for any challenges to be made by independent auditors. We leave the design of the specification and implementation of this system open as a future Cosmos improvement proposal, to be passed by the Cosmos Hub's governance system.</p> <p _v-db453a86=\"\">While the socio-political atmosphere is not quite evolved enough yet, we can extend the mechanism to allow for zones which peg to the fiat currency of a nation states by forming a validator set out of some combination of institutions responsible for the nation's currency, most particularly, its banks. Of course, extra precautions must be made to only accept currencies backed by strong legal systems that can enforce auditability of the banks' activities by a sufficiently large group of trusted notaries and institutions.</p> <p _v-db453a86=\"\">A result of this integration would be, for instance, the ability of anyone with a bank account at a participating bank to move dollars from their bank account, which is on the zone, to other accounts on the zone, or to the hub, or to another zone entirely. In this regard, the Cosmos Hub can act as a seamless conduit between fiat currencies and cryptocurrencies, removing the barriers that have until now limitted their interoperabiltiy to the realm of exchanges.</p> <h3 id=ethereum-scaling _v-db453a86=\"\">Ethereum Scaling</h3> <p _v-db453a86=\"\">An open issue for Ethereum is how to solve the scaling problem. Currently, Ethereum nodes process every single transaction and also stores all the state. <a href=\"https://docs.google.com/presentation/d/1CjD0W4l4-CwHKUvfF5Vlps76fKLEC6pIwu1a_kC_YRQ/mobilepresent?slide=id.gd284b9333_0_28\" _v-db453a86=\"\">link</a>.</p> <p _v-db453a86=\"\">Since Tendermint can commit blocks much faster than Ethereum's proof-of-work, EVM zones powered by Tendermint consensus and operating on pegged-ether can provide higher performance to Ethereum blockchains. Additionally, though the Cosmos hub and IBC packet mechanics does not allow for arbitrary contract logic execution per se, it can be used to coordinate token movements between Ethereum contracts running on different zones, providing a foundation for token-centric Ethereum scaling via sharding.</p> <h3 id=multi-application-integration _v-db453a86=\"\">Multi-Application Integration</h3> <p _v-db453a86=\"\">Cosmos zones run arbitrary application logic, defined at the beginning of the zone's life, and potentially updated over time by governance. Such flexibility allows Cosmos zones to act as pegs to other cryptocurrencies, like Ethereum or Bitcoin, but it also permits derivatives of those blockchains, utilizing the same codebase but with a different validator set and initial distribution. This allows many existing cryptocurrency frameworks, such as that of Ethereum, Zerocash, Bitcoin, CryptoNote, and so on to be used with a higher performance consensus engine (Tendermint Core) on a common network, openning tremendous opportunity for interoperability across platforms. Furthermore, as a multi-asset blockchain, a single transaction may contain multiple inputs and outputs, where each input can be any token type, enabling Cosmos to serve directly as a platform for decentralized exchange, though orders are assumed to be matched via other platforms. Alternatively, a zone can serve as a distributed fault-tolerant exchange (with orderbooks), which can be a strict improvement over existing centralized cryptocurrency exchanges which tend to get hacked over time. </p> <p _v-db453a86=\"\">Zones can also serve as blockchain-backed versions of enterprise and government systems, where pieces of a particular service, traditionally run by an organization or group of organizations, are instead run as a TMSP application on a certain zone, allowing it to inherit the security and interoperability of the public Cosmos network, without sacrificing control over the underlying service. Thus, Cosmos may offer the best of both worlds for organizations looking to utilize blockchain technology that are wary of relinquishing control completely to a (distributed) third party.</p> <h3 id=network-partition-mitigation _v-db453a86=\"\">Network Partition Mitigation</h3> <p _v-db453a86=\"\">Some claim that a major problem with consistency-favouring consensus algorithms like Tendermint is that any network partition which causes there to be no single partition with +⅔ voting power (e.g. ⅓+ goign offline) will halt consensus altogether. The Cosmos architecture can mitigate this problem by using a global hub with regional autonomous zones, where voting power for each zone are distributed based on a common geographic region. For instance, a common paradigm may be for individual cities, or regions, to operate their own zones while sharing a common hub (e.g. the Cosmos Hub), enabling municipal activity to persist in the event that the hub halts due to a temporary network partition. Note that this allows real geological, political, and network-topological features to be considered in designing robust federated fault-tolerant systems.</p> <h3 id=federated-name-resolution-system _v-db453a86=\"\">Federated Name Resolution System</h3> <p _v-db453a86=\"\">NameCoin was one of the first blockchains to attempt to solve the name-resolution problem by adapting the Bitcoin blockchain. Unfortunately there have been several issues with this approach.</p> <p _v-db453a86=\"\">With Namecoin, we can verify that say, <em _v-db453a86=\"\">@satoshi</em> was registered with a particular public key at some point in the past, but we wouldn’t know whether the public key had since been updated recently unless we download all the blocks since the last update of that name. This is due to the limitation of Bitcoin's UTXO transaction Merkle-ization model where only the transactions (but not mutable application state) are Merkle-ized into the block-hash -- which lets us prove existence, but not the non-existence of later updates to a name. Thus, we can't know for certain the most recent value of a name without trusting a full node, or incurring significant costs in bandwidth by downloading the whole blockchain.</p> <p _v-db453a86=\"\">Even if a Merkle-ized search tree were implemented in NameCoin, its dependency on proof-of-work makes light client verification problematic. Light clients must download a complete copy of the headers for all blocks in the entire blockchain (or at least all the headers since the last update to a name). This means that the bandwidth requirements scale linearly with the amount of time <a href=https://en.bitcoin.it/wiki/Thin_Client_Security _v-db453a86=\"\">[21]</a>. In addition, name-changes on a proof-of-work blockchain requires waiting for additional proof-of-work confirmation blocks, which can take up to an hour on Bitcoin.</p> <p _v-db453a86=\"\">With Tendermint, all we need is the most recent block-hash signed by a quorum of validators (by voting power), and a Merkle proof to the current value associated with the name. This makes it possible to have a succinct, quick, and secure light-client verification of name values.</p> <p _v-db453a86=\"\">In Cosmos, we can take this concept and extend it further. Each name-registration zone in Cosmos can have an associated top-level-domain (TLD) name such as \".com\" or \".org\", and each name-registration zone can have its own governance and registration rules.</p> <h2 id=issuance-and-incentives _v-db453a86=\"\">Issuance and Incentives</h2> <h3 id=the-atom-token _v-db453a86=\"\">The Atom Token</h3> <p _v-db453a86=\"\">While the Cosmos Hub is a multi-asset distributed ledger, there is a special native token called the <em _v-db453a86=\"\">atom</em>. Atoms are the only staking token of the Cosmos Hub. Atoms are a license for the holder to vote, validate, or delegate to other validators. Like Ethereum's ether, atoms can also be used to pay for transaction fees to mitigate spam. Additional inflationary atoms and block transaction fees are rewarded to validators and delegators who delegate to validators.</p> <p _v-db453a86=\"\">The <code _v-db453a86=\"\">BurnAtomTx</code> transaction can be used to recover any proportionate amount of tokens from the reserve pool.</p> <h4 id=crowdfund _v-db453a86=\"\">Crowdfund</h4> <p _v-db453a86=\"\">The initial distribution of atom tokens and validators on Genesis will go to the funders of the Cosmos Crowdsale (75%), pre-funders (5%), and Cosmos Corp (20%). From genesis onward, 1/3 of the total amount of atoms will be rewarded to bonded validators and delegators every year.</p> <p _v-db453a86=\"\">See the <a href=https://github.com/cosmos/cosmos/blob/master/PLAN.md _v-db453a86=\"\">Crowdfund Plan</a> for additional details.</p> <h4 id=vesting _v-db453a86=\"\">Vesting</h4> <p _v-db453a86=\"\">To prevent the crowdfund from attracting short-term speculators only interested in a pump-and-dump scheme, the genesis atoms will not be transferrable until they have vested. Each account will vest atoms over a period of 2 years at a constant rate every hour, determined by the total number of genesis atoms / (2 <em _v-db453a86=\"\"> 365 </em> 24) hours. Atoms earned by the inflationary block reward are pre-vested, and can be transferred immediately, so bonded validators and delegators can earn more than 1/2 of their genesis atoms after the first year.</p> <h3 id=limitations-on-the-number-of-validators _v-db453a86=\"\">Limitations on the Number of Validators</h3> <p _v-db453a86=\"\">Unlike Bitcoin or other proof-of-work blockchains, a Tendermint blockchain gets slower with more validators due to the increased communication complexity. Fortunately, we can support enough validators to make for a robust globally distributed blockchain with very fast transaction confirmation times, and, as bandwidth, storage, and parallel compute capacity increases, we will be able to support more validators in the future.</p> <p _v-db453a86=\"\">On genesis day, the maximum number of validators will be set to 100, and this number will increase at a rate of 13% for 10 years, and settle at 300 validators.</p> <pre _v-db453a86=\"\"><code _v-db453a86=\"\">Year 0: 100\nYear 1: 113\nYear 2: 127\nYear 3: 144\nYear 4: 163\nYear 5: 184\nYear 6: 208\nYear 7: 235\nYear 8: 265\nYear 9: 300\nYear 10: 300\n...\n</code></pre><h3 id=becoming-a-validator-after-genesis-day _v-db453a86=\"\">Becoming a Validator After Genesis Day</h3> <p _v-db453a86=\"\">Atom holders who are not already validators can become one by signing and submitting a <code _v-db453a86=\"\">BondTx</code> transaction. The amount of atoms provided as collateral must be nonzero. Anyone can become a validator at any time, except when the size of the current validator set is greater than the maximum number of validators allowed. In that case, the transaction is only valid if the amount of atoms is greater than the amount of effective atoms held by the smallest validator, where effective atoms include delegated atoms. When a new validator replaces an existing validator in such a way, the existing validator becomes inactive and all the atoms and delegated atoms enter the unbonding state.</p> <h3 id=penalties-for-validators _v-db453a86=\"\">Penalties for Validators</h3> <p _v-db453a86=\"\">There must be some penalty imposed on the validators for when they intentionally or unintentionally deviate from the sanctioned protocol. Some evidence is immediately admissible, such as a double-sign at the same height and round, or a violation of \"prevote-the-lock\" (a rule of the Tendermint consensus protocol). Such evidence will result in the validator losing its good standing and its bonded atoms as well its proportionate share of tokens in the reserve pool -- collectively called its \"stake\" -- will get slashed.</p> <p _v-db453a86=\"\">Sometimes, validators will not be available, either due to regional network disruptions, power failure, or other reasons. If, at any point in the past <code _v-db453a86=\"\">ValidatorTimeoutWindow</code> blocks, a validator's commit vote is not included in the blockchain more than <code _v-db453a86=\"\">ValidatorTimeoutMaxAbsent</code> times, that validator will become inactive, and lose <code _v-db453a86=\"\">ValidatorTimeoutPenalty</code> (DEFAULT 1%) of its stake.</p> <p _v-db453a86=\"\">Some \"malicious\" behavior do not produce obviously discernable evidence on the blockchain. In these cases, the validators can coordinate out of band to force the timeout of these malicious validators, if there is a supermajority consensus.</p> <p _v-db453a86=\"\">In situations where the Cosmos Hub halts due to a ⅓+ coalition of voting power going offline, or in situations where a ⅓+ coalition of voting power censor evidence of malicious behavior from entering the blockchain, the hub must recover with a hard-fork reorg-proposal. (Link to \"Forks and Censorship Attacks\").</p> <h3 id=transaction-fees _v-db453a86=\"\">Transaction Fees</h3> <p _v-db453a86=\"\">Cosmos Hub validators can accept any token type or combination of types as a fee for processing a transaction. Each validator can subjectively set whatever exchange rate it wants, and choose whatever transactions it wants, as long as the <code _v-db453a86=\"\">BlockGasLimit</code> is not exceeded. The collected fees minus any taxes specified below are redistributed to the bonded stakeholders in proportion to their bonded atoms, every <code _v-db453a86=\"\">ValidatorPayoutPeriod</code> (DEFAULT 1 hour).</p> <p _v-db453a86=\"\">Of the collected transaction fees, <code _v-db453a86=\"\">ReserveTax</code> (DEFAULT 2%) will go toward the reserve pool to increase the reserve pool and increase the security and value of the Cosmos network. Also, a <code _v-db453a86=\"\">CommonsTax</code> (DEFAULT 3%) will go toward the funding of common goods. These funds will go to the <code _v-db453a86=\"\">CustodianAddress</code> to be distributed in accordance with whatever is decided by the governance system.</p> <p _v-db453a86=\"\">Atom holders who delegate their voting power to other validators pay a commission to the delegated validator. The commission can be set by each validator.</p> <h3 id=incentivizing-hackers _v-db453a86=\"\">Incentivizing Hackers</h3> <p _v-db453a86=\"\">The security of the Cosmos Hub is a function of the security of the underlying validators and the choice of delegation by delegators. In order to encourage the discovery and early reporting of found vulnerabilities, the Cosmos Hub encourages hackers to publish successful exploits via a <code _v-db453a86=\"\">ReportHackTx</code> transaction that essentially says, \"This validator got hacked. Please send bounty to this address\". Upon such an exploit, the validator and delegators will become inactive, <code _v-db453a86=\"\">HackPunishmentRatio</code> (default 5%) of everyone's atoms will get slashed, and <code _v-db453a86=\"\">HackRewardRatio</code> (default 5%) of everyone's atoms will get rewarded to the hacker's bounty address. The validator must recover the remaining atoms by using their backup key.</p> <p _v-db453a86=\"\">In order to prevent this feature from being abused to transfer unvested atoms, the portion of vested vs unvested atoms of validators and delegators before and after the <code _v-db453a86=\"\">ReportHackTx</code> will remain the same, and the hacker bounty will include some unvested atoms, if any.</p> <h2 id=governance _v-db453a86=\"\">Governance</h2> <p _v-db453a86=\"\">The Cosmos Hub is operated by a distributed organization that requires a well defined governance mechanism in order to coordinate various changes to the blockchain, such as the variable parameters of the system, as well as software upgrades and constitutional amendments.</p> <p _v-db453a86=\"\">All validators are responsible for voting on all proposals. Failing to vote on a proposal in a timely manner will result in the validator becoming inactivated automatically for a period of time called the <code _v-db453a86=\"\">AbsenteeismPenaltyPeriod</code> (DEFAULT 1 week).</p> <p _v-db453a86=\"\">Delegators automatically inherit the vote of the delegated validator. This vote may be overridden manually. Unbonded atoms get no vote.</p> <p _v-db453a86=\"\">Each proposal requires a deposit of <code _v-db453a86=\"\">MinimumProposalDeposit</code> tokens, which may be a combination one or more tokens including atoms. For each proposal, the voters may vote to take the deposit. If more than half of the voters choose to take the deposit (e.g. because the proposal was spam), the deposit goes to the reserve pool, except any atoms which are burned.</p> <p _v-db453a86=\"\">For each proposal, voters may vote with the following options:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\">Yay</li> <li _v-db453a86=\"\">YayWithForce</li> <li _v-db453a86=\"\">Nay</li> <li _v-db453a86=\"\">NayWithForce</li> <li _v-db453a86=\"\">Abstain</li> </ul> <p _v-db453a86=\"\">A strict majority of Yay or YayWithForce votes (or Nay or NayWithForce votes) is required for the proposal to be decided as accepted (or decided as failed), but 1/3+ can veto the majority decision by voting \"with force\". When a strict majority is vetoed, everyone gets punished by losing <code _v-db453a86=\"\">VetoPenaltyFeeBlocks</code> (DEFAULT 1 day's worth of blocks) worth of fees (except taxes which will not be affected), and the party that vetoed the majority decision will be additionally punished by losing <code _v-db453a86=\"\">VetoPenaltyAtoms</code> (DEFAULT 0.1%) of its atoms.</p> <h3 id=parameter-change-proposal _v-db453a86=\"\">Parameter Change Proposal</h3> <p _v-db453a86=\"\">Any of the parameters defined here can be changed with the acceptance of a <code _v-db453a86=\"\">ParameterChangeProposal</code>.</p> <h3 id=text-proposal _v-db453a86=\"\">Text Proposal</h3> <p _v-db453a86=\"\">All other proposals, such as a proposal to upgrade the protocol, will be coordinated via the generic <code _v-db453a86=\"\">TextProposal</code>.</p> <h2 id=roadmap _v-db453a86=\"\">Roadmap</h2> <p _v-db453a86=\"\">See <a href=https://github.com/cosmos/cosmos/blob/master/PLAN.md _v-db453a86=\"\">the Plan</a>.</p> <h2 id=related-work _v-db453a86=\"\">Related Work</h2> <p _v-db453a86=\"\">There have been many innovations in blockchain consensus and scalability in the past couple of years. This section provides a brief survey of a select number of important ones.</p> <h3 id=consensus-systems _v-db453a86=\"\">Consensus Systems</h3> <h4 id=classic-byzantine-fault-tolerance _v-db453a86=\"\">Classic Byzantine Fault Tolerance</h4> <p _v-db453a86=\"\">Consensus in the presence of malicious participants is a problem dating back to the early 80s, when Leslie Lamport coined the phrase \"Byzantine fault\" to refer to arbitrary process behavior that deviates from the intended behavior, in contrast to a \"crash fault\", wherein a process simply crashes. Early solutions were discovered for synchronous networks where there is an upper bound on message latency, though pratical use was limited to highly controlled environments such as airplane controllers and datacenters synchronized via atomic clocks. It was not until the late 90s that Practical Byzantine Fault Tolerance (PBFT) <a href=http://pmg.csail.mit.edu/papers/osdi99.pdf _v-db453a86=\"\">[11]</a> was introduced as an efficient partially synchronous consensus algorithm able to tolerate up to ⅓ of processes behaving arbitrarily. PBFT became the standard algorithm, spawning many variations, including most recently by IBM as part of their contribution to Hyperledger.</p> <p _v-db453a86=\"\">The main benefit of Tendermint consensus over PBFT is that Tendermint has an improved and simplified underlying structure, some of which is a result of embracing the blockchain paradigm. Tendermint blocks must commit in order, which obviates the complexity and communication overhead associated with PBFT's view-changes. In Cosmos and many cryptocurrencies, there is no need to allow for block <em _v-db453a86=\"\">N+i</em> where <em _v-db453a86=\"\">i &gt;= 1</em> to commit, when block <em _v-db453a86=\"\">N</em> itself hasn't yet committed. If bandwidth is the reason why block <em _v-db453a86=\"\">N</em> hasn't committed in a Cosmos zone, then it doesn't help to use bandwidth sharing votes for blocks <em _v-db453a86=\"\">N+i</em>. If a network partition or offline nodes is the reason why block <em _v-db453a86=\"\">N</em> hasn't committed, then <em _v-db453a86=\"\">N+i</em> won't commit anyway.</p> <p _v-db453a86=\"\">In addition, the batching of transactions into blocks allows for regular Merkle-hashing of the application state, rather than periodic digests as with PBFT's checkpointing scheme. This allows for faster provable transaction commits for light-clients and faster inter-blockchain communication.</p> <p _v-db453a86=\"\">Tendermint Core also includes many optimizations and features that go above and beyond what is specified in PBFT. For example, the blocks proposed by validators are split into parts, Merkle-ized, and gossipped in such a way that improves broadcasting performance (see LibSwift <a href=http://www.ds.ewi.tudelft.nl/fileadmin/pds/papers/PerformanceAnalysisOfLibswift.pdf _v-db453a86=\"\">[19]</a> for inspiration). Also, Tendermint Core doesn't make any assumption about point-to-point connectivity, and functions for as long as the P2P network is weakly connected.</p> <h4 id=bitshares-delegated-stake _v-db453a86=\"\">BitShares delegated stake</h4> <p _v-db453a86=\"\">While not the first to deploy proof-of-stake (PoS), BitShares <a href=https://bitshares.org/technology/delegated-proof-of-stake-consensus/ _v-db453a86=\"\">[12]</a> contributed considerably to research and adoption of PoS blockchains, particularly those known as \"delegated\" PoS. In BitShares, stake holders elect \"witnesses\", responsible for ordering and committing transactions, and \"delegates\", responsible for co-ordinating software updates and parameter changes. Though BitShares achieves high performance (100k tx/s, 1s latency) in ideal conditions, it is subject to double spend attacks by malicious witnesses which fork the blockchain without suffering an explicit economic punishment -- it suffers from the \"nothing-at-stake\" problem. BitShares attempts to mitigate the problem by allowing transactions to refer to recent block-hashes. Additionally, stakeholders can remove or replace misbehaving witnesses on a daily basis, though this does nothing to explicitly punish a double-spend attack that was successful.</p> <h4 id=stellar _v-db453a86=\"\">Stellar</h4> <p _v-db453a86=\"\">Building on an approach pioneered by Ripple, Stellar <a href=https://www.stellar.org/papers/stellar-consensus-protocol.pdf _v-db453a86=\"\">[13]</a> refined a model of Federated Byzantine Agreement wherein the processes participating in consensus do not constitute a fixed and globally known set. Rather, each process node curates one or more \"quorum slices\" each constituting a set of trusted processes. A \"quorum\" in Stellar is defined to be a set of nodes that contain (is a superset of) at least one quorum slice for each node in the set, such that agreement can be reached.</p> <p _v-db453a86=\"\">The security of the Stellar mechanism relies on the assumption that the intersection of <em _v-db453a86=\"\">any</em> two quorums is non-empty, while the availability of a node requires at least one of its quorum slices to consist entirely of correct nodes, creating a trade-off between using large or small quorum-slices that may be difficult to balance without imposing significant assumptions about trust. Ultimately, nodes must somehow choose adequate quorum slices for there to be sufficient fault-tolerance (or any \"intact nodes\" at all, of which much of the results of the paper depend on), and the only provided strategy for ensuring such a configuration is heirarchical and similar to the Border Gateway Protocol (BGP), used by top-tier ISPs on the internet to establish global routing tables, and by that used by browsers to manage TLS certificates; both notorious for their insecurity.</p> <p _v-db453a86=\"\">The criticism in the Stellar paper of the Tendermint-based proof-of-stake systems is mitigated by the token strategy described here, wherein a new type of token called the <em _v-db453a86=\"\">atom</em> is issued that represent claims to future portions of fees and rewards. The advantage of Tendermint-based proof-of-stake, then, is its relative simplicity, while still providing sufficient, and provable security guarantees.</p> <h4 id=bitcoinng _v-db453a86=\"\">BitcoinNG</h4> <p _v-db453a86=\"\">BitcoinNG is a proposed improvement to Bitcoin that would allow for forms of vertical scalability, such as increasing the block size, without the negative economic consequences typically associated with such a change, such as the disproportionately large impact on small miners. This improvement is achieved by separating leader election from transaction broadcast: leaders are first elected by proof-of-work in \"micro-blocks\", and then able to broadcast transactions to be committed until a new micro-block is found. This reduces the bandwidth requirements necessary to win the PoW race, allowing small miners to more fairly compete, and allowing transactions to be committed more regularly by the last miner to find a micro-block.</p> <h4 id=casper _v-db453a86=\"\">Casper</h4> <p _v-db453a86=\"\">Casper <a href=https://blog.ethereum.org/2015/08/01/introducing-casper-friendly-ghost/ _v-db453a86=\"\">[16]</a> is a proposed proof-of-stake consensus algorithm for Ethereum. Its prime mode of operation is \"consensus-by-bet\". The idea is that by letting validators iteratively bet on which block it believes will become committed into the blockchain based on the other bets that it's seen so far, finality can be achieved eventually. <a href=https://blog.ethereum.org/2015/12/28/understanding-serenity-part-2-casper/ _v-db453a86=\"\">link</a>. This is an active area of research by the Casper team. The challenge is in constructing a betting mechanism that can be proven to be an evolutionarily stable strategy. The main benefit of Casper as compared to Tendermint may be in offering \"availability over consistency\" -- consensus does not require a +⅔ quorum of voting power -- perhaps at the cost of commit speed or implementation complexity.</p> <h3 id=horizontal-scaling _v-db453a86=\"\">Horizontal Scaling</h3> <h4 id=interledger-protocol _v-db453a86=\"\">Interledger Protocol</h4> <p _v-db453a86=\"\">The Interledger protocol <a href=https://interledger.org/rfcs/0001-interledger-architecture/ _v-db453a86=\"\">[14]</a> is not strictly a scalability solution. It provides an adhoc interoperation between different ledger systems through a loosely coupled bilateral relationship network. Like the Lightning Network, the purpose of ILP is to facilitate payments, but it specifically focuses on payments across disparate ledger types, and extends the atomic transaction mechanism to include not only hash-locks, but also a quroum of notaries (called the Atomic Transport Protocol). The latter mechanism for enforcing atomicity in inter-ledger transactions is similar to Tendermint's light-client SPV echanism, so an illustration of the distinction between ILP and Cosmos/IBC is warranted, and provided below.</p> <ol _v-db453a86=\"\"> <li _v-db453a86=\"\"><p _v-db453a86=\"\">The notaries of a connector in ILP does not support membership changes, and does not allow for flexible weighting between notaries. On the other hand, IBC is designed specifically for blockchains, where validators can have different weights, and where membership can change over the course of the blockchain.</p> </li> <li _v-db453a86=\"\"><p _v-db453a86=\"\">As in the Lightning Network, the receiver of payment in ILP must online to send a confirmation back to the sender. In a token transfer over IBC, the validator-set of the receiver's blockchain is responsible for providing confirmation, not the receiving user.</p> </li> <li _v-db453a86=\"\"><p _v-db453a86=\"\">The most striking difference is that ILP's connectors are not responsible or keeping authoritative state about payments, whereas in Cosmos, the validators of a hub are the authority of the state of IBC token transfers as well as the authority of the amount of tokens held by each zone (but not the amount of tokens held by each account within a zone). This is he fundamental innovation that allows for secure asymmetric tranfer of tokens from zone to zone; the analog to ILP's connector in Cosmos is a persistent and maximally secure blockchain ledger, the Cosmos Hub.</p> </li> <li _v-db453a86=\"\"><p _v-db453a86=\"\">The inter-ledger payments in ILP need to be backed by an exchange orderbook, as there is no asymmetric transfer of coins from one ledger to another, only the transfer of value or market equivalents.</p> </li> </ol> <h4 id=sidechains _v-db453a86=\"\">Sidechains</h4> <p _v-db453a86=\"\">Sidechains <a href=https://blockstream.com/sidechains.pdf _v-db453a86=\"\">[15]</a> are a proposed mechanism for scaling the Bitcoin network via alternative blockchains that are \"pegged\" to the Bitcoin blockchain. Sidechains allow bitcoins to effectively move from the Bitcoin blockchain to the sidechain and back, and allow for experimentation in new features on the sidechain. As in the Cosmos Hub, the sidechain and Bitcoin serve as light-clients of each other, using SPV proofs to determine when coins should be transferred to the sidechain and back. Of course, since Bitcoin uses proof-of-work, sidechains centered around Bitcoin suffer from the many problems and risks of proof-of-work as a consensus mechanism. Furthermore, this is a Bitcoin-maximalist solution that doesn't natively support a variety of tokens and inter-zone network topology as Cosmos does. That said, the core mechanism of the two-way peg is in principle the same as that employed by the Cosmos network.</p> <h4 id=ethereum-scalability-efforts _v-db453a86=\"\">Ethereum Scalability Efforts</h4> <p _v-db453a86=\"\">Ethereum is currently researching a number of different strategies to shard the state of the Ethereum blockchain to address scalability needs. These efforts have the goal of maintaining the abstraction layer offered by the current Ethereum Virtual Machine across the shared state space. Multiple research efforts are underway at this time. <a href=https://github.com/ethereum/EIPs/issues/53 _v-db453a86=\"\">[18]</a><a href=http://vitalik.ca/files/mauve_paper.html _v-db453a86=\"\">[22]</a></p> <h5 id=cosmos-vs-ethereum-2-0-mauve _v-db453a86=\"\">Cosmos vs Ethereum 2.0 Mauve</h5> <p _v-db453a86=\"\">Cosmos and Ethereum 2.0 Mauve <a href=http://vitalik.ca/files/mauve_paper.html _v-db453a86=\"\">[22]</a> have different design goals.</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\">Cosmos is specifically about tokens. Mauve is about scaling general computation.</li> <li _v-db453a86=\"\">Cosmos is not bound to the EVM, so even different VMs can interoperate.</li> <li _v-db453a86=\"\">Cosmos lets the zone creator determine who validates the zone.</li> <li _v-db453a86=\"\">Anyone can start a new zone in Cosmos (unless governance decides otherwise).</li> <li _v-db453a86=\"\">The hub isolates zone failures so global token invariants are preserved.</li> </ul> <h3 id=general-scaling _v-db453a86=\"\">General Scaling</h3> <h4 id=lightning-network _v-db453a86=\"\">Lightning Network</h4> <p _v-db453a86=\"\">The Lightning Network is a proposed token transfer network operating at a layer above the Bitcoin blockchain (and other public blockchains), enabling many orders of magnitude improvement in transaction throughput by moving the majority of transactions outside of the consensus ledger into so-called \"payment channels\". This is made possible by on-chain cryptocurrency scripts, which enables parties to enter into bilateral stateful contracts where the state can be updated by sharing digital signatures, and contracts can be closed by finally publishing evidence onto the blockchain, a mechanism first popularized by cross-chain atomic swaps. By openning payment channels with many parties, participants in the Lightning Network can become focal points for routing the payments of others, leading to a fully connected payment channel network, at the cost of capital being tied up on payment channels.</p> <p _v-db453a86=\"\">While the Lightning Network can also easily extend across multiple independent blockchains to allow for the transfer of <em _v-db453a86=\"\">value</em> via an exchange market, it cannot be used to assymetrically transfer <em _v-db453a86=\"\">tokens</em> from one blockchain to another. The main benefit of the Cosmos network described here is to enable such direct token transfers. That said, we expect payment channels and the Lightning Network to become widely adopted along with our token transfer mechanism, for cost-saving and privacy reasons.</p> <h4 id=segregated-witness _v-db453a86=\"\">Segregated Witness</h4> <p _v-db453a86=\"\">Segregated Witness is a Bitcoin improvement proposal <a href=https://github.com/bitcoin/bips/blob/master/bip-0141.mediawiki _v-db453a86=\"\">link</a> that aims to increase the per-block tranasction throughput 2X or 3X, while simultaneously making block syncing faster for new nodes. The brilliance of this solution is in how it works within the limitations of Bitcoin's current protocol and allows for a soft-fork upgrade (i.e. clients with older versions of the software will continue to function after the upgrade). Tendermint being a new protocol has no design restrictions, so it has a different scaling priorities. Primarily, Tendermint uses a BFT round-robin algorithm based on cryptographic signatures instead of mining, which trivially allows horizontal scaling through multiple parallel blockchains, while regular, more frequent block commits allow for vertical scaling as well.</p> <hr _v-db453a86=\"\"> <h2 id=appendix _v-db453a86=\"\">Appendix</h2> <h3 id=fork-accountability _v-db453a86=\"\">Fork Accountability</h3> <p _v-db453a86=\"\">A well designed consensus protocol should provide some guarantees in the event that the tolerance capacity is exceeded and the consensus fails. This is especially necessary in economic systems, where Byzantine behaviour can have substantial financial reward. The most important such guarantee is a form of <em _v-db453a86=\"\">fork-accountability</em>, where the processes that caused the consensus to fail (ie. caused clients of the protocol to accept different values - a fork) can be identified and punished according to the rules of the protocol, or, possibly, the legal system. When the legal system is unreliable or excessively expensive to invoke, validators can be forced to make security deposits in order to participate, and those deposits can be revoked, or slashed, when malicious behaviour is detected <a href=https://blog.ethereum.org/2014/01/15/slasher-a-punitive-proof-of-stake-algorithm/ _v-db453a86=\"\">[10]</a>.</p> <p _v-db453a86=\"\">Note this is much unlike Bitcoin, where forking is a regular occurence due to network asynchrony and the probabilistic nature of finding partial hash collissions. Since in many cases, a malicious fork is indistinguishable from a fork due to asynchrony, Bitcoin can not reliably implement fork-accountability, other than the implicit opportunity cost paid by miners for mining an orphaned block.</p> <h3 id=tendermint-consensus _v-db453a86=\"\">Tendermint Consensus</h3> <p _v-db453a86=\"\">We call the voting stages <em _v-db453a86=\"\">PreVote</em> and <em _v-db453a86=\"\">PreCommit</em>. A vote can be for a particular block or for <em _v-db453a86=\"\">Nil</em>. We call a collection of +⅔ PreVotes for a single block in the same round a <em _v-db453a86=\"\">Polka</em>, and a collection of +⅔ PreCommits for a single block in the same round a <em _v-db453a86=\"\">Commit</em>. If +⅔ PreCommit for Nil in the same round, they move to the next round.</p> <p _v-db453a86=\"\">Note that strict determinism in the protocol incurs a weak synchrony assumption as faulty leaders must be detected and skipped. Thus, validators wait some amount of time <em _v-db453a86=\"\">TimeoutPropose</em> before they Prevote Nil, and the value of TimeoutPropose increases with each round. Progression through the rest of a round is fully asychronous, in that progress is only made once a validator hears from +⅔ of the network. In practice, it would take an extremely strong adversary to indefinetely thwart the weak synchrony assumption (causing the consensus to fail to ever commit a block), and doing so can be made even more difficult by using randomized values of TimeoutPropose on each validator.</p> <p _v-db453a86=\"\">An additional set of constraints, or Locking Rules, ensure that the network will eventually commit just one block at each height. Any malicious attempt to cause more than one block to be committed at a given height can be identified. First, a PreCommit for a block must come with justification, in the form of a Polka for that block. If the validator has already PreCommit a block at round <em _v-db453a86=\"\">R_1</em>, we say they are <em _v-db453a86=\"\">locked</em> on that block, and the Polka used to justify the new PreCommit at round <em _v-db453a86=\"\">R_2</em> must come in a round <em _v-db453a86=\"\">R_polka</em> where <em _v-db453a86=\"\">R_1 &lt; R_polka &lt;= R_2</em>. Second, validators must Propose and/or PreVote the block they are locked on. Together, these conditions ensure that a validator does not PreCommit without sufficient evidence as justification, and that validators which have already PreCommit cannot contribute to evidence to PreCommit something else. This ensures both safety and liveness of the consensus algorithm.</p> <p _v-db453a86=\"\">The full details of the protocol are described <a href=https://github.com/tendermint/tendermint/wiki/Byzantine-Consensus-Algorithm _v-db453a86=\"\">here</a>.</p> <h3 id=tendermint-light-clients _v-db453a86=\"\">Tendermint Light Clients</h3> <p _v-db453a86=\"\">The need to sync all block headers is eliminated in Tendermint-PoS as the existence of an alternative chain (a fork) means ⅓+ of bonded stake can be slashed. Of course, since slashing requires that <em _v-db453a86=\"\">someone</em> share evidence of a fork, light clients should store any block-hash commits that it sees. Additionally, light clients could periodically stay synced with changes to the validator set, in order to avoid <a href=#preventing-long-range-attacks _v-db453a86=\"\">long range attacks</a> (but other solutions are possible).</p> <p _v-db453a86=\"\">In spirit similar to Ethereum, Tendermint enables applications to embed a global Merkle root hash in each block, allowing easily verifiable state queries for things like account balances, the value stored in a contract, or the existence of an unspent transaction output, depending on the nature of the application.</p> <h3 id=preventing-long-range-attacks _v-db453a86=\"\">Preventing Long Range Attacks</h3> <p _v-db453a86=\"\">Assuming a sufficiently resilient collection of broadcast networks and a static validator set, any fork in the blockchain can be detected and the deposits of the offending validators slashed. This innovation, first suggested by Vitalik Buterin in early 2014, solves the nothing-at-stake problem of other proof-of-stake cryptocurrencies (see <a href=#related-work _v-db453a86=\"\">Related Work</a>). However, since validator sets must be able to change, over a long range of time the original validators may all become unbonded, and hence would be free to create a new chain, from the genesis block, incurring no cost as they no longer have deposits locked up. This attack came to be known as the Long Range Attack (LRA) in contrast to a Short Range Attack, where validators who are currently bonded cause a fork and are hence punishable (assuming a fork-accountable BFT algorithm like Tendermint consensus). Long Range Attacks are often thought to be a critical blow to proof-of-stake.</p> <p _v-db453a86=\"\">Fortunately, the LRA can be mitigated as follows. First, for a validator to unbond (thereby recovering their collateral deposit and no longer earning fees to participate in the consensus), the deposit must be made untransferable for an amount of time known as the \"unbonding period\", which may be on the order of weeks or months. Second, for a light client to be secure, the first time it connects to the network it must verify a recent block-hash against a trusted source, or preferably multiple of them. This condition is sometimes referred to as \"weak subjectivity\". Finally, to remain secure, it must sync up with the latest validator set at least as frequently as the length of the unbonding period. This ensures that the light client knows about changes to the validator set before a validator has its capital unbonded and thus no longer at stake, which would allow it to deceive the client by carrying out a long range attack by creating new blocks beginning back at a height where it was bonded (assuming it has control of sufficiently many of the early private keys).</p> <p _v-db453a86=\"\">Note that overcoming the LRA in this way requires an overhaul of the original security model of proof-of-work. In PoW, it is assumed that a light client can sync to the current height from the trusted genesis block at any time simply by processing the proof-of-work in every block header. To overcome the LRA, however, we require that a light client come online with some regularity to track changes in the validator set, and that the first time they come online they must be particularly careful to authenticate what they hear from the network against trusted sources. Of course, this latter requirement is similar to that of Bitcoin, where the protocol and software must also be obtained from a trusted source.</p> <p _v-db453a86=\"\">The above method for preventing LRA is well suited for validators and full nodes of a Tendermint-powered blockchain because these nodes are meant to remain connected to the network. The method is also suitable for light clients that can be expected to sync with the network frequently. However, for light clients that are not expected to have frequent access to the internet or the blockchain network, yet another solution can be used to overcome the LRA. Non-validator token holders can post their tokens as collateral with a very long unbonding period (e.g. much longer than the unbonding period for validators) and serve light clients with a secondary method of attesting to the validity of current and past block-hashes. While these tokens do not count toward the security of the blockchain's consensus, they nevertheless can provide strong guarantees for light clients. If historical block-hash querying were supported in Ethereum, anyone could bond their tokens in a specially designed smart contract and provide attestation services for pay, effectively creating a market for light-client LRA security.</p> <h3 id=overcoming-forks-and-censorship-attacks _v-db453a86=\"\">Overcoming Forks and Censorship Attacks</h3> <p _v-db453a86=\"\">Due to the definition of a block commit, any ⅓+ coalition of voting power can halt the blockchain by going offline or not broadcasting their votes. Such a coalition can also censor particular transactions by rejecting blocks that include these transactions, though this would result in a significant proportion of block proposals to be rejected, which would slow down the rate of block commits of the blockchain, reducing its utility and value. The malicious coalition might also broadcast votes in a trickle so as to grind blockchain block commits to a near halt, or engage in any combination of these attacks. Finally, it can cause the blockchain to fork, by double-signing or violating the locking rules.</p> <p _v-db453a86=\"\">If a global active adversary were also involved, it can partition the network in such a way that it may appear that the wrong subset of validators were responsible for the slowdown. This is not just a limitation of Tendermint, but rather a limitation of all consensus protocols whose network is potentially controlled by an active adversary.</p> <p _v-db453a86=\"\">For these types of attacks, a subset of the validators should coordinate through external means to sign a reorg-proposal that chooses a fork (and any evidence thereof) and the initial subset of validators with their signatures. Validators who sign such a reorg-proposal forego their collateral on all other forks. Clients should verify the signatures on the reorg-proposal, verify any evidence, and make a judgement or prompt the end-user for a decision. For example, a phone wallet app may prompt the user with a security warning, while a refrigerator may accept any reorg-proposal signed by +½ of the original validators by voting power.</p> <p _v-db453a86=\"\">No non-synchronous Byzantine fault-tolerant algorithm can come to consensus when ⅓+ of voting power are dishonest, yet a fork assumes that ⅓+ of voting power have already been dishonest by double-signing or lock-changing without justification. So, signing the reorg-proposal is a coordination problem that cannot be solved by any non-synchronous protocol (i.e. automatically, and without making assumptions about the reliability of the underlying network). For now, we leave the problem of reorg-proposal coordination to human coordination via social consensus on internet media. Validators must take care to ensure that there are no remaining network partitions prior to signing a reorg-proposal, to avoid situations where two conflicting reorg-proposals are signed.</p> <p _v-db453a86=\"\">Assuming that the external coordination medium and protocol is robust, it follows that forks are less of a concern than censorship attacks.</p> <p _v-db453a86=\"\">In addition to forks and censorship, which require ⅓+ Byzantine voting power, a coalition of +⅔ voting power may commit arbitrary, invalid state. This is characteristic of any (BFT) consensus system. Unlike double-signing, which creates forks with easily verifiable evidence, detecting committment of an invalid state requires non-validating peers to verify whole blocks, which implies that they keep a local copy of the state and execute each transaction, computing the state root independently for themselves. Once detected, the only way to handle such a failure is via social consensus. For instance, in situations where Bitcoin has failed, whether forking due to software bugs (as in March 2013), or committing invalid state due to Byzantine behavior of miners (as in July 2015), the well connected community of businesses, developers, miners, and other organizations established a social consensus as to what manual actions were required by participants to heal the network. Furthermore, since validators of a Tendermint blockchain may be expected to be identifiable, commitment of an invalid state may even be punishable by law or some external jurisprudence, if desired.</p> <h3 id=tmsp-specification _v-db453a86=\"\">TMSP specification</h3> <p _v-db453a86=\"\">TMSP consists of 3 primary message types that get delivered from the core to the application. The application replies with corresponding response messages.</p> <p _v-db453a86=\"\">The <code _v-db453a86=\"\">AppendTx</code> message is the work horse of the application. Each transaction in the blockchain is delivered with this message. The application needs to validate each transactions received with the AppendTx message against the current state, application protocol, and the cryptographic credentials of the transaction. A validated transaction then needs to update the application state — by binding a value into a key values store, or by updating the UTXO database.</p> <p _v-db453a86=\"\">The <code _v-db453a86=\"\">CheckTx</code> message is similar to AppendTx, but it’s only for validating transactions. Tendermint Core’s mempool first checks the validity of a transaction with CheckTx, and only relays valid transactions to its peers. Applications may check an incrementing nonce in the transaction and return an error upon CheckTx if the nonce is old.</p> <p _v-db453a86=\"\">The <code _v-db453a86=\"\">Commit</code> message is used to compute a cryptographic commitment to the current application state, to be placed into the next block header. This has some handy properties. Inconsistencies in updating that state will now appear as blockchain forks which catches a whole class of programming errors. This also simplifies the development of secure lightweight clients, as Merkle-hash proofs can be verified by checking against the block-hash, and the block-hash is signed by a quorum of validators (by voting power).</p> <p _v-db453a86=\"\">Additional TMSP messages allow the application to keep track of and change the validator set, and for the application to receive the block information, such as the height and the commit votes. </p> <p _v-db453a86=\"\">TMSP requests/responses are simple Protobuf messages. Check out the <a href=https://github.com/tendermint/tmsp/blob/master/types/types.proto _v-db453a86=\"\">schema file</a>.</p> <h4 id=appendtx _v-db453a86=\"\">AppendTx</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Arguments</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Data ([]byte)</code>: The request transaction bytes</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Returns</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Code (uint32)</code>: Response code</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Data ([]byte)</code>: Result bytes, if any</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Log (string)</code>: Debug or error message</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Usage</strong>:<br _v-db453a86=\"\"> Append and run a transaction. If the transaction is valid, returns CodeType.OK</li> </ul> <h4 id=checktx _v-db453a86=\"\">CheckTx</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Arguments</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Data ([]byte)</code>: The request transaction bytes</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Returns</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Code (uint32)</code>: Response code</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Data ([]byte)</code>: Result bytes, if any</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Log (string)</code>: Debug or error message</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Usage</strong>:<br _v-db453a86=\"\"> Validate a transaction. This message should not mutate the state. Transactions are first run through CheckTx before broadcast to peers in the mempool layer. You can make CheckTx semi-stateful and clear the state upon <code _v-db453a86=\"\">Commit</code> or <code _v-db453a86=\"\">BeginBlock</code>, to allow for dependent sequences of transactions in the same block.</li> </ul> <h4 id=commit _v-db453a86=\"\">Commit</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Returns</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Data ([]byte)</code>: The Merkle root hash</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Log (string)</code>: Debug or error message</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Usage</strong>:<br _v-db453a86=\"\"> Return a Merkle root hash of the application state.</li> </ul> <h4 id=query _v-db453a86=\"\">Query</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Arguments</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Data ([]byte)</code>: The query request bytes</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Returns</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Code (uint32)</code>: Response code</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Data ([]byte)</code>: The query response bytes</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Log (string)</code>: Debug or error message</li> </ul> </li> </ul> <h4 id=flush _v-db453a86=\"\">Flush</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Usage</strong>:<br _v-db453a86=\"\"> Flush the response queue. Applications that implement <code _v-db453a86=\"\">types.Application</code> need not implement this message -- it's handled by the project.</li> </ul> <h4 id=info _v-db453a86=\"\">Info</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Returns</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Data ([]byte)</code>: The info bytes</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Usage</strong>:<br _v-db453a86=\"\"> Return information about the application state. Application specific.</li> </ul> <h4 id=setoption _v-db453a86=\"\">SetOption</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Arguments</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Key (string)</code>: Key to set</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Value (string)</code>: Value to set for key</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Returns</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Log (string)</code>: Debug or error message</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Usage</strong>:<br _v-db453a86=\"\"> Set application options. E.g. Key=\"mode\", Value=\"mempool\" for a mempool connection, or Key=\"mode\", Value=\"consensus\" for a consensus connection. Other options are application specific.</li> </ul> <h4 id=initchain _v-db453a86=\"\">InitChain</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Arguments</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Validators ([]Validator)</code>: Initial genesis-validators</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Usage</strong>:<br _v-db453a86=\"\"> Called once upon genesis</li> </ul> <h4 id=beginblock _v-db453a86=\"\">BeginBlock</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Arguments</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Height (uint64)</code>: The block height that is starting</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Usage</strong>:<br _v-db453a86=\"\"> Signals the beginning of a new block. Called prior to any AppendTxs.</li> </ul> <h4 id=endblock _v-db453a86=\"\">EndBlock</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Arguments</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Height (uint64)</code>: The block height that ended</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Returns</strong>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Validators ([]Validator)</code>: Changed validators with new voting powers (0 to remove)</li> </ul> </li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Usage</strong>:<br _v-db453a86=\"\"> Signals the end of a block. Called prior to each Commit after all transactions</li> </ul> <p _v-db453a86=\"\">See <a href=https://github.com/tendermint/tmsp#message-types _v-db453a86=\"\">the TMSP repository</a> for more details.</p> <h3 id=ibc-packet-delivery-acknowledgement _v-db453a86=\"\">IBC Packet Delivery Acknowledgement</h3> <p _v-db453a86=\"\">There are several reasons why a sender may want the acknowledgement of delivery of a packet by the receiving chain. For example, the sender may not know the status of the destination chain, if it is expected to be faulty. Or, the sender may want to impose a timeout on the packet (with the <code _v-db453a86=\"\">MaxHeight</code> packet field), while any destination chain may suffer from a denial-of-service attack with a sudden spike in the number of incoming packets.</p> <p _v-db453a86=\"\">In these cases, the sender can require delivery acknowledgement by setting the initial packet status to <code _v-db453a86=\"\">AckPending</code>. Then, it is the receiving chain's responsibility to confirm delivery by including an abbreviated<code _v-db453a86=\"\">IBCPacket</code> in the app Merkle hash.</p> <p _v-db453a86=\"\"><img src=https://raw.githubusercontent.com/gnuclear/atom-whitepaper/master/msc/ibc_with_ack.png alt=\"Figure of Zone1, Zone2, and Hub IBC with\nacknowledgement\" _v-db453a86=\"\"></p> <p _v-db453a86=\"\">First, an <code _v-db453a86=\"\">IBCBlockCommit</code> and <code _v-db453a86=\"\">IBCPacketTx</code> are posted on \"Hub\" that proves the existence of an <code _v-db453a86=\"\">IBCPacket</code> on \"Zone1\". Say that <code _v-db453a86=\"\">IBCPacketTx</code> has the following value:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromChainID</code>: \"Zone1\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromBlockHeight</code>: 100 (say)</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Packet</code>: an <code _v-db453a86=\"\">IBCPacket</code>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Header</code>: an <code _v-db453a86=\"\">IBCPacketHeader</code>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">SrcChainID</code>: \"Zone1\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">DstChainID</code>: \"Zone2\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Number</code>: 200 (say)</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Status</code>: <code _v-db453a86=\"\">AckPending</code></li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Type</code>: \"coin\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">MaxHeight</code>: 350 (say \"Hub\" is currently at height 300)</li> </ul> </li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Payload</code>: &lt;The bytes of a \"coin\" payload&gt;</li> </ul> </li> </ul> <p _v-db453a86=\"\">Next, an <code _v-db453a86=\"\">IBCBlockCommit</code> and <code _v-db453a86=\"\">IBCPacketTx</code> are posted on \"Zone2\" that proves the existence of an <code _v-db453a86=\"\">IBCPacket</code> on \"Hub\". Say that <code _v-db453a86=\"\">IBCPacketTx</code> has the following value:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromChainID</code>: \"Hub\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromBlockHeight</code>: 300</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Packet</code>: an <code _v-db453a86=\"\">IBCPacket</code>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Header</code>: an <code _v-db453a86=\"\">IBCPacketHeader</code>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">SrcChainID</code>: \"Zone1\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">DstChainID</code>: \"Zone2\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Number</code>: 200</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Status</code>: <code _v-db453a86=\"\">AckPending</code></li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Type</code>: \"coin\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">MaxHeight</code>: 350</li> </ul> </li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Payload</code>: &lt;The same bytes of a \"coin\" payload&gt;</li> </ul> </li> </ul> <p _v-db453a86=\"\">Next, \"Zone2\" must include in its app-hash an abbreviated packet that shows the new status of <code _v-db453a86=\"\">AckSent</code>. An <code _v-db453a86=\"\">IBCBlockCommit</code> and <code _v-db453a86=\"\">IBCPacketTx</code> are posted back on \"Hub\" that proves the existence of an abbreviated <code _v-db453a86=\"\">IBCPacket</code> on \"Zone2\". Say that <code _v-db453a86=\"\">IBCPacketTx</code> has the following value:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromChainID</code>: \"Zone2\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromBlockHeight</code>: 400 (say)</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Packet</code>: an <code _v-db453a86=\"\">IBCPacket</code>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Header</code>: an <code _v-db453a86=\"\">IBCPacketHeader</code>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">SrcChainID</code>: \"Zone1\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">DstChainID</code>: \"Zone2\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Number</code>: 200</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Status</code>: <code _v-db453a86=\"\">AckSent</code></li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Type</code>: \"coin\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">MaxHeight</code>: 350</li> </ul> </li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">PayloadHash</code>: &lt;The hash bytes of the same \"coin\" payload&gt;</li> </ul> </li> </ul> <p _v-db453a86=\"\">Finally, \"Hub\" must update the status of the packet from <code _v-db453a86=\"\">AckPending</code> to <code _v-db453a86=\"\">AckReceived</code>. Evidence of this new finalized status should go back to \"Zone2\". Say that <code _v-db453a86=\"\">IBCPacketTx</code> has the following value:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromChainID</code>: \"Hub\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromBlockHeight</code>: 301</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Packet</code>: an <code _v-db453a86=\"\">IBCPacket</code>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Header</code>: an <code _v-db453a86=\"\">IBCPacketHeader</code>:<ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">SrcChainID</code>: \"Zone1\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">DstChainID</code>: \"Zone2\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Number</code>: 200</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Status</code>: <code _v-db453a86=\"\">AckReceived</code></li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Type</code>: \"coin\"</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">MaxHeight</code>: 350</li> </ul> </li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">PayloadHash</code>: &lt;The hash bytes of the same \"coin\" payload&gt;</li> </ul> </li> </ul> <p _v-db453a86=\"\">Meanwhile, \"Zone1\" may optimistically assume successful delivery of a \"coin\" packet unless evidence to the contrary is proven on \"Hub\". In the example above, if \"Hub\" had not received an <code _v-db453a86=\"\">AckSent</code> status from \"Zone2\" by block 350, it would have set the status automatically to <code _v-db453a86=\"\">Timeout</code>. This evidence of a timeout can get posted back on \"Zone1\", and any tokens can be returned.</p> <p _v-db453a86=\"\"><img src=https://raw.githubusercontent.com/gnuclear/atom-whitepaper/master/msc/ibc_with_ack_timeout.png alt=\"Figure of Zone1, Zone2, and Hub IBC with acknowledgement and\ntimeout\" _v-db453a86=\"\"></p> <h3 id=merkle-tree-proof-specification _v-db453a86=\"\">Merkle Tree &amp; Proof Specification</h3> <p _v-db453a86=\"\">There are two types of Merkle trees supported in the Tendermint/Cosmos ecosystem: The Simple Tree, and the IAVL+ Tree.</p> <h4 id=simple-tree _v-db453a86=\"\">Simple Tree</h4> <p _v-db453a86=\"\">The Simple Tree is a Merkle tree for a static list of elements. If the number of items is not a power of two, some leaves will be at different levels. Simple Tree tries to keep both sides of the tree the same height, but the left may be one greater. This Merkle tree is used to Merkle-ize the transactions of a block, and the top level elements of the application state root.</p> <pre _v-db453a86=\"\"><code _v-db453a86=\"\">                *\n               / \\\n             /     \\\n           /         \\\n         /             \\\n        *               *\n       / \\             / \\\n      /   \\           /   \\\n     /     \\         /     \\\n    *       *       *       h6\n   / \\     / \\     / \\\n  h0  h1  h2  h3  h4  h5\n\n  A SimpleTree with 7 elements\n</code></pre><h4 id=iavl-tree _v-db453a86=\"\">IAVL+ Tree</h4> <p _v-db453a86=\"\">The purpose of the IAVL+ data structure is to provide persistent storage for key-value pairs in the application state such that a deterministic Merkle root hash can be computed efficiently. The tree is balanced using a variant of the <a href=http://en.wikipedia.org/wiki/AVL_tree _v-db453a86=\"\">AVL algortihm</a>, and all operations are O(log(n)).</p> <p _v-db453a86=\"\">In an AVL tree, the heights of the two child subtrees of any node differ by at most one. Whenever this condition is violated upon an update, the tree is rebalanced by creating O(log(n)) new nodes that point to unmodified nodes of the old tree. In the original AVL algorithm, inner nodes can also hold key-value pairs. The AVL+ algorithm (note the plus) modifies the AVL algorithm to keep all values on leaf nodes, while only using branch-nodes to store keys. This simplifies the algorithm while keeping the merkle hash trail short.</p> <p _v-db453a86=\"\">The AVL+ Tree is analogous to Ethereum's <a href=http://en.wikipedia.org/wiki/Radix_tree _v-db453a86=\"\">Patricia tries</a>. There are tradeoffs. Keys do not need to be hashed prior to insertion in IAVL+ trees, so this provides faster ordered iteration in the key space which may benefit some applications. The logic is simpler to implement, requiring only two types of nodes -- inner nodes and leaf nodes. The Merkle proof is on average shorter, being a balanced binary tree. On the other hand, the Merkle root of an IAVL+ tree depends on the order of updates.</p> <p _v-db453a86=\"\">We will support additional efficient Merkle trees, such as Ethereum's Patricia Trie when the binary variant becomes available.</p> <h3 id=transaction-types _v-db453a86=\"\">Transaction Types</h3> <p _v-db453a86=\"\">In the canonical implementation, transactions are streamed to the Cosmos hub application via the TMSP interface.</p> <p _v-db453a86=\"\">The Cosmos Hub will accept a number of primary transaction types, including <code _v-db453a86=\"\">SendTx</code>, <code _v-db453a86=\"\">BondTx</code>, <code _v-db453a86=\"\">UnbondTx</code>, <code _v-db453a86=\"\">ReportHackTx</code>, <code _v-db453a86=\"\">SlashTx</code>, <code _v-db453a86=\"\">BurnAtomTx</code>, <code _v-db453a86=\"\">ProposalCreateTx</code>, and <code _v-db453a86=\"\">ProposalVoteTx</code>, which are fairly self-explanatory and will be documented in a future revision of this paper. Here we document the two primary transaction types for IBC: <code _v-db453a86=\"\">IBCBlockCommitTx</code> and <code _v-db453a86=\"\">IBCPacketTx</code>.</p> <h4 id=ibcblockcommittx _v-db453a86=\"\">IBCBlockCommitTx</h4> <p _v-db453a86=\"\">An <code _v-db453a86=\"\">IBCBlockCommitTx</code> transaction is composed of:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">ChainID (string)</code>: The ID of the blockchain</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">BlockHash ([]byte)</code>: The block-hash bytes, the Merkle root which includes the app-hash</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">BlockPartsHeader (PartSetHeader)</code>: The block part-set header bytes, only needed to verify vote signatures</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">BlockHeight (int)</code>: The height of the commit</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">BlockRound (int)</code>: The round of the commit</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Commit ([]Vote)</code>: The +⅔ Tendermint <code _v-db453a86=\"\">Precommit</code> votes that comprise a block commit</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">ValidatorsHash ([]byte)</code>: A Merkle-tree root hash of the new validator set</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">ValidatorsHashProof (SimpleProof)</code>: A SimpleTree Merkle-proof for proving the <code _v-db453a86=\"\">ValidatorsHash</code> against the <code _v-db453a86=\"\">BlockHash</code></li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">AppHash ([]byte)</code>: A IAVLTree Merkle-tree root hash of the application state</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">AppHashProof (SimpleProof)</code>: A SimpleTree Merkle-proof for proving the <code _v-db453a86=\"\">AppHash</code> against the <code _v-db453a86=\"\">BlockHash</code></li> </ul> <h4 id=ibcpackettx _v-db453a86=\"\">IBCPacketTx</h4> <p _v-db453a86=\"\">An <code _v-db453a86=\"\">IBCPacket</code> is composed of:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Header (IBCPacketHeader)</code>: The packet header</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Payload ([]byte)</code>: The bytes of the packet payload. <em _v-db453a86=\"\">Optional</em></li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">PayloadHash ([]byte)</code>: The hash for the bytes of the packet. <em _v-db453a86=\"\">Optional</em></li> </ul> <p _v-db453a86=\"\">Either one of <code _v-db453a86=\"\">Payload</code> or <code _v-db453a86=\"\">PayloadHash</code> must be present. The hash of an <code _v-db453a86=\"\">IBCPacket</code> is a simple Merkle root of the two items, <code _v-db453a86=\"\">Header</code> and <code _v-db453a86=\"\">Payload</code>. An <code _v-db453a86=\"\">IBCPacket</code> without the full payload is called an <em _v-db453a86=\"\">abbreviated packet</em>.</p> <p _v-db453a86=\"\">An <code _v-db453a86=\"\">IBCPacketHeader</code> is composed of:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">SrcChainID (string)</code>: The source blockchain ID</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">DstChainID (string)</code>: The destination blockchain ID</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Number (int)</code>: A unique number for all packets</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Status (enum)</code>: Can be one of <code _v-db453a86=\"\">AckPending</code>, <code _v-db453a86=\"\">AckSent</code>, <code _v-db453a86=\"\">AckReceived</code>, <code _v-db453a86=\"\">NoAck</code>, or <code _v-db453a86=\"\">Timeout</code></li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Type (string)</code>: The types are application-dependent. Cosmos reserves the \"coin\" packet type</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">MaxHeight (int)</code>: If status is not <code _v-db453a86=\"\">NoAckWanted</code> or <code _v-db453a86=\"\">AckReceived</code> by this height, status becomes <code _v-db453a86=\"\">Timeout</code>. <em _v-db453a86=\"\">Optional</em></li> </ul> <p _v-db453a86=\"\">An <code _v-db453a86=\"\">IBCPacketTx</code> transaction is composed of:</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromChainID (string)</code>: The ID of the blockchain which is providing this packet; not necessarily the source</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">FromBlockHeight (int)</code>: The blockchain height in which the following packet is included (Merkle-ized) in the block-hash of the source chain</li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">Packet (IBCPacket)</code>: A packet of data, whose status may be one of <code _v-db453a86=\"\">AckPending</code>, <code _v-db453a86=\"\">AckSent</code>, <code _v-db453a86=\"\">AckReceived</code>, <code _v-db453a86=\"\">NoAck</code>, or <code _v-db453a86=\"\">Timeout</code></li> <li _v-db453a86=\"\"><code _v-db453a86=\"\">PacketProof (IAVLProof)</code>: A IAVLTree Merkle-proof for proving the packet's hash against the <code _v-db453a86=\"\">AppHash</code> of the source chain at given height</li> </ul> <p _v-db453a86=\"\">The sequence for sending a packet from \"Zone1\" to \"Zone2\" through the \"Hub\" is depicted in {Figure X}. First, an <code _v-db453a86=\"\">IBCPacketTx</code> proves to \"Hub\" that the packet is included in the app-state of \"Zone1\". Then, another <code _v-db453a86=\"\">IBCPacketTx</code> proves to \"Zone2\" that the packet is included in the app-state of \"Hub\". During this procedure, the <code _v-db453a86=\"\">IBCPacket</code> fields are identical: the <code _v-db453a86=\"\">SrcChainID</code> is always \"Zone1\", and the <code _v-db453a86=\"\">DstChainID</code> is always \"Zone2\".</p> <p _v-db453a86=\"\">The <code _v-db453a86=\"\">PacketProof</code> must have the correct Merkle-proof path, as follows:</p> <pre _v-db453a86=\"\"><code _v-db453a86=\"\">IBC/&lt;SrcChainID&gt;/&lt;DstChainID&gt;/&lt;Number&gt;\n</code></pre><p _v-db453a86=\"\">When \"Zone1\" wants to send a packet to \"Zone2\" through \"Hub\", the <code _v-db453a86=\"\">IBCPacket</code> data are identical whether the packet is Merkle-ized on \"Zone1\", the \"Hub\", or \"Zone2\". The only mutable field is <code _v-db453a86=\"\">Status</code> for tracking delivery, as shown below.</p> <h2 id=acknowledgements _v-db453a86=\"\">Acknowledgements</h2> <p _v-db453a86=\"\">We thank our friends and peers for assistance in conceptualizing, reviewing, and providing support for our work with Tendermint and Cosmos.</p> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=https://github.com/zmanian _v-db453a86=\"\">Zaki Manian</a> of <a href=https://www.skuchain.com/ _v-db453a86=\"\">SkuChain</a> provided much help in formatting and wording, especially under the TMSP section</li> <li _v-db453a86=\"\"><a href=https://github.com/jtremback _v-db453a86=\"\">Jehan Tremback</a> of Althea and Dustin Byington for helping with initial iterations</li> <li _v-db453a86=\"\"><a href=http://soc1024.com/ _v-db453a86=\"\">Andrew Miller</a> of <a href=https://eprint.iacr.org/2016/199 _v-db453a86=\"\">Honey Badger</a> for feedback on consensus</li> <li _v-db453a86=\"\"><a href=https://fixingtao.com/ _v-db453a86=\"\">Greg Slepak</a> for feedback on consensus and wording</li> <li _v-db453a86=\"\">Also thanks to <a href=https://github.com/gleim _v-db453a86=\"\">Bill Gleim</a> and <a href=http://www.seunghwanhan.com _v-db453a86=\"\">Seunghwan Han</a> for various contributions.</li> <li _v-db453a86=\"\"><strong _v-db453a86=\"\">Your name and organization here for your contribution</strong></li> </ul> <h2 id=citations _v-db453a86=\"\">Citations</h2> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=https://bitcoin.org/bitcoin.pdf _v-db453a86=\"\">1</a> Bitcoin: <a href=https://bitcoin.org/bitcoin.pdf _v-db453a86=\"\">https://bitcoin.org/bitcoin.pdf</a></li> <li _v-db453a86=\"\"><a href=http://zerocash-project.org/paper _v-db453a86=\"\">2</a> ZeroCash: <a href=http://zerocash-project.org/paper _v-db453a86=\"\">http://zerocash-project.org/paper</a></li> <li _v-db453a86=\"\"><a href=https://github.com/ethereum/wiki/wiki/White-Paper _v-db453a86=\"\">3</a> Ethereum: <a href=https://github.com/ethereum/wiki/wiki/White-Paper _v-db453a86=\"\">https://github.com/ethereum/wiki/wiki/White-Paper</a></li> <li _v-db453a86=\"\"><a href=https://download.slock.it/public/DAO/WhitePaper.pdf _v-db453a86=\"\">4</a> TheDAO: <a href=https://download.slock.it/public/DAO/WhitePaper.pdf _v-db453a86=\"\">https://download.slock.it/public/DAO/WhitePaper.pdf</a></li> <li _v-db453a86=\"\"><a href=https://github.com/bitcoin/bips/blob/master/bip-0141.mediawiki _v-db453a86=\"\">5</a> Segregated Witness: <a href=https://github.com/bitcoin/bips/blob/master/bip-0141.mediawiki _v-db453a86=\"\">https://github.com/bitcoin/bips/blob/master/bip-0141.mediawiki</a></li> <li _v-db453a86=\"\"><a href=https://arxiv.org/pdf/1510.02037v2.pdf _v-db453a86=\"\">6</a> BitcoinNG: <a href=https://arxiv.org/pdf/1510.02037v2.pdf _v-db453a86=\"\">https://arxiv.org/pdf/1510.02037v2.pdf</a></li> <li _v-db453a86=\"\"><a href=https://lightning.network/lightning-network-paper-DRAFT-0.5.pdf _v-db453a86=\"\">7</a> Lightning Network: <a href=https://lightning.network/lightning-network-paper-DRAFT-0.5.pdf _v-db453a86=\"\">https://lightning.network/lightning-network-paper-DRAFT-0.5.pdf</a></li> <li _v-db453a86=\"\"><a href=https://github.com/tendermint/tendermint/wiki _v-db453a86=\"\">8</a> Tendermint: <a href=https://github.com/tendermint/tendermint/wiki _v-db453a86=\"\">https://github.com/tendermint/tendermint/wiki</a></li> <li _v-db453a86=\"\"><a href=https://groups.csail.mit.edu/tds/papers/Lynch/jacm85.pdf _v-db453a86=\"\">9</a> FLP Impossibility: <a href=https://groups.csail.mit.edu/tds/papers/Lynch/jacm85.pdf _v-db453a86=\"\">https://groups.csail.mit.edu/tds/papers/Lynch/jacm85.pdf</a></li> <li _v-db453a86=\"\"><a href=https://blog.ethereum.org/2014/01/15/slasher-a-punitive-proof-of-stake-algorithm/ _v-db453a86=\"\">10</a> Slasher: <a href=https://blog.ethereum.org/2014/01/15/slasher-a-punitive-proof-of-stake-algorithm/ _v-db453a86=\"\">https://blog.ethereum.org/2014/01/15/slasher-a-punitive-proof-of-stake-algorithm/</a></li> <li _v-db453a86=\"\"><a href=http://pmg.csail.mit.edu/papers/osdi99.pdf _v-db453a86=\"\">11</a> PBFT: <a href=http://pmg.csail.mit.edu/papers/osdi99.pdf _v-db453a86=\"\">http://pmg.csail.mit.edu/papers/osdi99.pdf</a></li> <li _v-db453a86=\"\"><a href=https://bitshares.org/technology/delegated-proof-of-stake-consensus/ _v-db453a86=\"\">12</a> BitShares: <a href=https://bitshares.org/technology/delegated-proof-of-stake-consensus/ _v-db453a86=\"\">https://bitshares.org/technology/delegated-proof-of-stake-consensus/</a></li> <li _v-db453a86=\"\"><a href=https://www.stellar.org/papers/stellar-consensus-protocol.pdf _v-db453a86=\"\">13</a> Stellar: <a href=https://www.stellar.org/papers/stellar-consensus-protocol.pdf _v-db453a86=\"\">https://www.stellar.org/papers/stellar-consensus-protocol.pdf</a></li> <li _v-db453a86=\"\"><a href=https://interledger.org/rfcs/0001-interledger-architecture/ _v-db453a86=\"\">14</a> Interledger: <a href=https://interledger.org/rfcs/0001-interledger-architecture/ _v-db453a86=\"\">https://interledger.org/rfcs/0001-interledger-architecture/</a></li> <li _v-db453a86=\"\"><a href=https://blockstream.com/sidechains.pdf _v-db453a86=\"\">15</a> Sidechains: <a href=https://blockstream.com/sidechains.pdf _v-db453a86=\"\">https://blockstream.com/sidechains.pdf</a></li> <li _v-db453a86=\"\"><a href=https://blog.ethereum.org/2015/08/01/introducing-casper-friendly-ghost/ _v-db453a86=\"\">16</a> Casper: <a href=https://blog.ethereum.org/2015/08/01/introducing-casper-friendly-ghost/ _v-db453a86=\"\">https://blog.ethereum.org/2015/08/01/introducing-casper-friendly-ghost/</a></li> <li _v-db453a86=\"\"><a href=https://github.com/tendermint/tmsp _v-db453a86=\"\">17</a> TMSP: <a href=https://github.com/tendermint/tmsp _v-db453a86=\"\">https://github.com/tendermint/tmsp</a></li> <li _v-db453a86=\"\"><a href=https://github.com/ethereum/EIPs/issues/53 _v-db453a86=\"\">18</a> Ethereum Sharding: <a href=https://github.com/ethereum/EIPs/issues/53 _v-db453a86=\"\">https://github.com/ethereum/EIPs/issues/53</a></li> <li _v-db453a86=\"\"><a href=http://www.ds.ewi.tudelft.nl/fileadmin/pds/papers/PerformanceAnalysisOfLibswift.pdf _v-db453a86=\"\">19</a> LibSwift: <a href=http://www.ds.ewi.tudelft.nl/fileadmin/pds/papers/PerformanceAnalysisOfLibswift.pdf _v-db453a86=\"\">http://www.ds.ewi.tudelft.nl/fileadmin/pds/papers/PerformanceAnalysisOfLibswift.pdf</a></li> <li _v-db453a86=\"\"><a href=http://groups.csail.mit.edu/tds/papers/Lynch/jacm88.pdf _v-db453a86=\"\">20</a> DLS: <a href=http://groups.csail.mit.edu/tds/papers/Lynch/jacm88.pdf _v-db453a86=\"\">http://groups.csail.mit.edu/tds/papers/Lynch/jacm88.pdf</a></li> <li _v-db453a86=\"\"><a href=https://en.bitcoin.it/wiki/Thin_Client_Security _v-db453a86=\"\">21</a> Thin Client Security: <a href=https://en.bitcoin.it/wiki/Thin_Client_Security _v-db453a86=\"\">https://en.bitcoin.it/wiki/Thin_Client_Security</a></li> <li _v-db453a86=\"\"><a href=http://vitalik.ca/files/mauve_paper.html _v-db453a86=\"\">22</a> Ethereum 2.0 Mauve Paper: <a href=http://vitalik.ca/files/mauve_paper.html _v-db453a86=\"\">http://vitalik.ca/files/mauve_paper.html</a></li> </ul> <h4 id=unsorted-links _v-db453a86=\"\">Unsorted links</h4> <ul _v-db453a86=\"\"> <li _v-db453a86=\"\"><a href=https://www.docdroid.net/ec7xGzs/314477721-ethereum-platform-review-opportunities-and-challenges-for-private-and-consortium-blockchains.pdf.html _v-db453a86=\"\">https://www.docdroid.net/ec7xGzs/314477721-ethereum-platform-review-opportunities-and-challenges-for-private-and-consortium-blockchains.pdf.html</a></li> </ul> </div> ";


